@use 'sass:color';

.client-filters {
	&__box {
		margin-bottom: 12px;
	}
	&__input {
		position: relative;
		input:not([type='checkbox'], [type='radio']),
		select {
			min-width: 200px;
			@include input('');
			&:focus,
			&:active,
			&:target {
				@include input-focus('');
			}
		}

		&--select {
			@include select-wrapper('');
		}
		&--search {
			@include search-wrapper('');
		}
	}
}

.client-list {
	margin-top: 32px;
	@include mq($max-width: 767.98px) {
		margin-top: 16px;
	}
}

.client-card {
	position: relative;
	min-height: 320px;
	height: 100%;
	display: block;
	border-radius: 20px;
	background-color: #ffffff;
	overflow: hidden;
	cursor: pointer;
	text-decoration: none;
	&:hover,
	&:target {
		.client-card__background-img img {
			transform: scale(1.1);
		}
	}

	&__background-img {
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100%;
		border-radius: inherit;
		// Overlay
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(360deg, rgba(51, 51, 51, 0) 0%, #333333 45%);
			border-radius: inherit;
		}
		img {
			object-fit: cover;
			position: absolute;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			transition: all 0.3s linear;
		}
	}
	&__content {
		position: relative;
		padding: 24px;
		display: flex;
		flex-direction: column;
		gap: 12px;
	}
	&__body {
		flex: 1 0 auto;
	}
	&__footer {
		flex: 0;
	}
	&__logo {
		padding: 16px;
		width: 72px;
		height: 72px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
		border-radius: 16px;
		img {
			max-width: 100%;
			max-height: 100%;
			object-fit: contain;
		}
	}
	&__title {
		font-family: $font-family-secondary;
		font-weight: 700;
		font-size: 20px;
		line-height: 1.25;
		color: #fff;
	}

	&__read-more {
		display: inline-flex;
		align-items: center;
		font-family: $font-family-secondary;
		font-weight: 700;
		font-size: 16px;
		line-height: 1.25;
		color: #fff;
		transition: all 0.3s ease-in-out;
		&:after {
			// Arrow right
			content: '\f061';
			font-family: 'Font Awesome 5 Free';
			font-size: 12px;
			line-height: 1;
			font-weight: 700;
			margin-left: 8px;
		}
	}
}
