.footer {
	position: relative;
	padding: 64px 0;
	flex-grow: 0;
	color: #96949a;
	background-color: $color-dark;
	overflow: hidden;
	&__top {
		padding-bottom: 24px;
	}
	&__bottom {
		padding-top: 24px;
		border-top: 1px solid $strokes;
	}
	&__row {
		justify-content: space-between;
		--bs-gutter-y: 64px;
	}
	&__logo {
		display: inline-block;
		margin-bottom: 64px;
		@media (min-width: 768px) {
			margin-bottom: 72px;
		}
	}
	&__title {
		font-size: 18px;
		line-height: 24px;
		font-weight: 400;
		color: #abacad;
		text-transform: uppercase;
		min-height: 2lh;
	}
	&__nav {
		--bs-gutter-y: 64px;
	}
	&__list {
		margin-top: 16px;
		font-size: 16px;
		font-weight: 400;
		line-height: 20px;
		li:not(:last-child) {
			margin-bottom: 24px;
		}
		a {
			color: #fff;
			text-decoration: none;
			&:hover {
				color: #3e63f5;
			}
		}
	}
	&__badges {
		margin-top: 64px;
		justify-content: center;
		display: flex;
		gap: 16px;
		@media (min-width: 992px) {
			margin-top: 32px;
			justify-content: flex-end;
		}
		img {
			height: 40px;
			width: auto;
			@media (min-width: 576px) {
				height: 64px;
			}
		}
	}
	&__form {
		@extend .subscribe-form;
	}
	&__social {
		margin-top: 24px;
		color: #fff;
		display: flex;
		gap: 12px;
		justify-content: center;
		@media (min-width: 576px) {
			gap: 20px;
		}
		@media (min-width: 992px) {
			justify-content: flex-start;
			margin-top: 8px;
		}
		a {
			color: #fff;
			font-size: 28px;
			text-decoration: none;
			&:hover {
				opacity: 0.75;
			}
		}
	}
	&__form-text {
		text-align: center;
		font-size: 12px;
	}
	&__copyright {
		text-align: center;
		font-size: 16px;
		margin-top: 20px;
	}
}
