.pms-growth {
	display: flex;
	flex-direction: column;
	gap: 32px;
	@media (min-width: 768px) {
		flex-direction: row;
		gap: 48px;
	}
	&__path {
		flex: 0 0 auto;
		display: flex;
		align-items: center;
		justify-content: center;
		transform: rotate(90deg);
		@media (min-width: 768px) {
			padding-top: 112px;
			flex-direction: row;
			transform: none;
		}
	}
	&__path-icon {
		width: 52px;
		height: 52px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.growth-card {
	&__header {
		display: flex;
		gap: 16px;
	}
	&__icon {
		background: $card-bg;
		border-radius: 16px;
		width: 72px;
		height: 72px;
		padding: 16px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&__title {
		margin-bottom: 8px;
	}
	&__body {
		margin-top: 40px;
		background: $card-bg;
		border-radius: 16px;
		padding: 24px;
		@media (min-width: 992px) {
			padding: 40px;
		}
	}
	&__list {
		padding-top: 32px;
		--list-gap: 32px;
	}
}

.why-salesforce {
	text-align: center;
	@media (min-width: 768px) {
		text-align: initial;
	}
	&__row {
		display: flex;
		justify-content: center;
		@media (min-width: 768px) {
			justify-content: space-between;
		}
	}
	&__title {
		margin-bottom: 32px;
	}
	&__image {
		max-width: 100%;
		height: auto;
	}
}

.sf-card {
	padding: 24px;
	background-color: $card-bg;
	border-radius: 16px;
	min-height: 260px;
	background-image: url('../img/icons/sf-cloud.svg');
	background-repeat: no-repeat;
	background-position: right 24px bottom 24px;
	@media (max-width: 576px) {
		&:nth-child(2) {
			background-color: #e5e1fc;
		}
		&:nth-child(3) {
			background-color: #d4e0ff;
		}
		&:nth-child(4) {
			background-color: #e3eeff;
		}
	}
	&__header {
		display: flex;
		align-items: center;
		gap: 12px;
		span {
			text-transform: uppercase;
			font-weight: 600;
			font-size: 12px;
			line-height: 20px;
			color: $strokes;
		}
	}
	&__icon {
		width: 44px;
		height: 44px;
		flex: 0 0 auto;
		border-radius: 8px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $color-primary-light;
		img {
			max-width: 24px;
			max-height: 24px;
		}
	}

	&__body {
		margin-top: 24px;
		p {
			margin-top: 12px;
		}
	}
}
