.process-page-top {
	height: auto;
	background: none;
	padding: 60px 0;
	@include mq($max-width: 1200px) {
		padding-bottom: 0;
	}
	&:before,
	&:after {
		display: none;
	}
	h1 {
		@include mq($max-width: 768px) {
			padding-top: 25px;
			font-size: 28px;
		}
	}
	h1:after {
		display: none;
	}
	p {
		color: #414141;
		line-height: 1.33;
		letter-spacing: 0.18px;
		padding-bottom: 30px;
	}
}
.process-page {
	color: #202020;
	font-size: 18px;
	font-weight: 300;
	line-height: 1.67;
	letter-spacing: 0.9px;
	@include mq($max-width: 768px) {
		font-size: 16px;
	}
	h2 {
		font-size: 48px;
		font-weight: 500;
		line-height: 2.4;
		@include mq($max-width: 768px) {
			font-size: 22px;
		}
	}
	.title {
		font-size: 36px;
		font-weight: 500;
		text-align: inherit;
		padding-bottom: 30px;
		color: #202020;
		letter-spacing: 0.18px;
		@include mq($max-width: 768px) {
			font-size: 20px;
		}
	}
	.subtitle {
		padding-bottom: 0;
		text-align: inherit;
		font-size: 30px;
		font-weight: 300;
		line-height: normal;
		@include mq($max-width: 768px) {
			font-size: 18px;
		}
	}
}
.process-before {
	margin-bottom: 150px;
	position: relative;
	border-radius: 5px;
	box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.05);
	z-index: 2;
	line-height: 1.33;
	@include mq($max-width: 992px) {
		margin-bottom: 100px;
	}
	@include mq($max-width: 556px) {
		margin-bottom: 50px;
	}
	&:before {
		content: '';
		position: absolute;
		height: 234px;
		width: 234px;
		bottom: -65px;
		right: -56px;
		box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.05);
		border-radius: 5px;
		background-color: #fbcd04;
		z-index: 1;
		@include mq($max-width: 567px) {
			display: none;
		}
	}
	&__wrapper {
		padding: 60px 50px 70px;
		position: relative;
		z-index: 2;
		background-color: #fff;
		@include mq($max-width: 567px) {
			padding: 0;
		}
		&:before {
			content: '';
			position: absolute;
			top: 20px;
			bottom: 26px;
			left: 22px;
			background-color: #f5f5f5;
			width: 10%;
			z-index: 3;
			@include mq($max-width: 567px) {
				display: none;
			}
		}
	}
	&__content {
		position: relative;
		padding: 20px 20px 30px;
		background-color: #fff;
		z-index: 3;
	}
}
.process-conclusion {
	padding-top: 40px;
	padding-bottom: 55px;
	p {
		padding-bottom: 30px;
	}
}
