@use 'sass:color';

.slider {
	display: flex;
	align-items: center;
	&__range {
		$thumb-radius: 50%;
		$thumb-size: 18px;
		$thumb-color: $color-primary;
		$thumb-color-hover: color.adjust($thumb-color, $lightness: -10%);
		$thumb-color-focus: color.adjust($thumb-color, $lightness: -10%);
		$thumb-color-active: color.adjust($thumb-color, $lightness: -20%);
		$thumb-shadow: rgba(0, 0, 0, 0.16) 0 2px 3px;
		$track-height: 6px;
		$track-color-background: #ddeaf8;
		$track-radius: calc($track-height / 2);
		$disabled-color: #f3f3f3;

		-moz-appearance: none;
		-webkit-appearance: none;
		appearance: none;
		width: 100%;
		background: none;
		background-color: transparent;
		border-radius: $track-radius;

		&:focus {
			outline: 0;
		}

		&::-webkit-slider-thumb {
			-moz-appearance: none;
			-webkit-appearance: none;
			appearance: none;
			width: $thumb-size;
			height: $thumb-size;
			border-radius: $thumb-radius;
			background: none;
			background-color: $thumb-color;
			border: 0;
			-webkit-box-shadow: $thumb-shadow;
			box-shadow: $thumb-shadow;
			cursor: pointer;
			-webkit-transition: background 0.15s ease-in-out;
			transition: background 0.15s ease-in-out;
			margin-top: calc(($track-height / 2) - ($thumb-size / 2));
		}

		&::-webkit-slider-thumb:hover {
			background-color: $thumb-color-hover;
		}

		&::-webkit-slider-runnable-track {
			width: 100%;
			height: $track-height;
			cursor: pointer;
			background: none;
			background-color: $track-color-background;
			border-radius: $track-radius;
		}

		&::-moz-range-thumb {
			-moz-appearance: none;
			-webkit-appearance: none;
			appearance: none;
			width: $thumb-size;
			height: $thumb-size;
			border-radius: $thumb-radius;
			background: none;
			background-color: $thumb-color;
			border: 0;
			box-shadow: $thumb-shadow;
			cursor: pointer;
			-moz-transition: background 0.15s ease-in-out;
			transition: background 0.15s ease-in-out;
		}

		&::-moz-range-thumb:hover {
			background-color: $thumb-color-hover;
		}

		&::-moz-range-track {
			width: 100%;
			height: $track-height;
			cursor: pointer;
			background: none;
			background-color: $track-color-background;
			border-radius: $track-radius;
		}

		&::-ms-track {
			width: 100%;
			height: $track-height;
			background: none;
			background-color: $track-color-background;
			border-radius: $track-radius;
			border-color: transparent;
			color: transparent;
			cursor: pointer;
		}

		&::-ms-thumb {
			-moz-appearance: none;
			-webkit-appearance: none;
			appearance: none;
			width: $thumb-size;
			height: $thumb-size;
			border-radius: $thumb-radius;
			background: none;
			background-color: $thumb-color;
			border: 0;
			box-shadow: $thumb-shadow;
			cursor: pointer;
			-ms-transition: background 0.15s ease-in-out;
			transition: background 0.15s ease-in-out;
			margin-top: 1px;
		}

		&::-ms-thumb:hover {
			background-color: $thumb-color-hover;
		}

		&:focus::-webkit-slider-thumb {
			background-color: $thumb-color-focus;
		}

		&:active::-webkit-slider-thumb {
			background-color: $thumb-color-active;
		}

		&:focus::-moz-range-thumb {
			background-color: $thumb-color-focus;
		}

		&:active::-moz-range-thumb {
			background-color: $thumb-color-active;
		}

		&[disabled]::-webkit-slider-thumb {
			background-color: $disabled-color;
			cursor: default;
		}

		&[disabled]::-webkit-slider-runnable-track {
			background-color: $disabled-color;
			cursor: default;
		}

		&[disabled]::-moz-range-thumb {
			background-color: $disabled-color;
			cursor: default;
		}

		&[disabled]::-moz-range-track {
			background-color: $disabled-color;
		}

		&[disabled]::-ms-thumb {
			background-color: $disabled-color;
			cursor: default;
		}

		&[disabled]::-ms-track {
			background-color: $disabled-color;
			cursor: default;
		}
	}
	// :not was added to override the default styles above
	&__value-input:not([type='range']) {
		font-size: 16px;
		min-width: 64px;
		flex: 0;
		@include input('');
	}
	&__input-container {
		flex: 0;
	}
}
