.video {
	padding: 53px 0;
	text-align: center;
	&__title {
		font-size: 48px;
		font-weight: 700;
		letter-spacing: 0.48px;
		text-align: center;
		padding-bottom: 50px;
		color: #212121;
		@include mq($max-width: 767px) {
			font-size: 25px;
			padding-bottom: 20px;
		}
	}
	&__img {
		display: inline-block;
		position: relative;
		border-radius: 20px;
		overflow: hidden;
		img {
			display: block;
			position: relative;
			z-index: 1;
			max-width: 100%;
		}
		&:before {
			content: '';
			position: absolute;
			inset: 0;
			background-color: rgba(0, 0, 0, 0.2);
			z-index: 2;
			@include transition(all 0.5s);
		}
		&:after {
			content: '';
			background: url(../img/icons/play.svg) no-repeat center;
			position: absolute;
			inset: 0;
			z-index: 3;
			@include transition(all 0.5s);
		}
		&:hover {
			&:before {
				background-color: rgba(0, 0, 0, 0.4);
			}
			&:after {
				transform: scale(1.2);
			}
		}
	}
}
