.about-page-top {
	background: #f6f6f6 url(../img/about-bg-top.png) no-repeat center;
	height: 437px;
	padding-top: 25px;
	color: #414141;
	font-size: 18px;
	font-weight: 300;
	position: relative;
	@include mq($max-width: 992px) {
		height: auto;
		text-align: center;
		padding-bottom: 200px;
	}
	&:after {
		content: '';
		position: absolute;
		left: 50%;
		top: 50%;
		width: 220px;
		height: 220px;
		border: 50px solid #fbcd04;
		border-radius: 50%;
		background: #f6f6f6;
		transform: translateX(-50%) translateY(-50%);
		@include mq($max-width: 992px) {
			width: 150px;
			height: 150px;
			bottom: 1%;
			transform: translateX(-50%);
			top: auto;
		}
	}
	&:before {
		position: absolute;
		transform: translateX(-50%);
		left: 50%;
		width: 9px;
		height: 50%;
		background: #fbcd04;
		bottom: 0;
		content: '';
		@include mq($max-width: 992px) {
			height: 10%;
		}
	}
	&.about-page-top-noline {
		&:before {
			display: none;
		}
	}
	article {
		position: relative;
		z-index: 2;
	}
	h1 {
		color: #2e3949;
		font-size: 50px;
		font-weight: 900;
		padding-bottom: 30px;
		margin: 0;
		position: relative;
		&:after {
			position: relative;
			top: 15px;
			content: url(../img/about-icon-title.png);
		}
		@include mq($max-width: 767px) {
			font-size: 24px;
			padding-bottom: 15px;
		}
	}
	h5 {
		color: #2e3949;
		font-size: 30px;
		font-weight: 700;
		padding-bottom: 25px;
		@include mq($max-width: 992px) {
			font-size: 18px;
		}
	}
	p {
		padding-bottom: 15px;
	}
}
.about-page-content {
	background: url(../img/about-bg-big.png) no-repeat bottom 350px left -500px, url(../img/bg-about.png) repeat;
	color: #0f0f0f;
	font-size: 18px;
	font-weight: 300;
	line-height: 1.3;
	@include mq($max-width: 767px) {
		font-size: 14px;
	}
	.video__img {
		width: 100%;
		display: block;
		margin: 20px 0;
		img {
			max-width: 100%;
		}
	}
	&__title {
		text-align: center;
		color: #212020;
		font-size: 36px;
		font-weight: 700;
		letter-spacing: 1.8px;
		padding-bottom: 50px;
		span {
			background: #fff;
			position: relative;
			display: inline-block;
		}
		@include mq($max-width: 767px) {
			font-size: 24px;
			padding-bottom: 15px;
		}
	}
	&__center {
		p {
			padding-bottom: 50px;
			@include mq($max-width: 767px) {
				padding-bottom: 20px;
			}
		}
		.text_center {
			padding-bottom: 30px;
			@include mq($max-width: 767px) {
				padding-bottom: 15px;
			}
		}
	}
	&__big-title-item {
		color: #171717;
		font-size: 80px;
		font-weight: 900;
		line-height: 1;
		height: 130px;
		display: flex;
		border-radius: 30px;
		align-items: center;
		justify-content: center;
		box-shadow: 0 0 16px rgba(251, 205, 4, 0.53);
		text-transform: uppercase;
		letter-spacing: 5px;
		text-align: center;
		background: #fbcd04 url(../img/about-bg-title.png) repeat center;
		margin-bottom: 30px;
		@include mq($max-width: 992px) {
			font-size: 40px;
			height: auto;
			padding: 30px 15px;
		}
		@include mq($max-width: 767px) {
			font-size: 24px;
			letter-spacing: 1px;
		}
	}
	&__big-title {
		padding-bottom: 100px;

		font-size: 20px;
	}
	&__person {
		font-size: 20px;
		article {
			color: #191919;
			font-size: 20px;
			font-weight: 300;
		}
	}
	&__person-text {
		padding-top: 105px;
		background: url(../img/person-text-bg.png) no-repeat left top;
		position: relative;
		@include mq($max-width: 767px) {
			padding-top: 30px;
		}
		&:before {
			content: '';
			width: 8px;
			height: calc(100% - 148px);
			background: #fbcd04;
			position: absolute;
			left: -25px;
			top: 148px;
			border-radius: 4px;
		}
		article {
			margin-bottom: 60px;
			position: relative;
			&:before {
				position: absolute;
				width: 24px;
				height: 24px;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.13);
				top: 50%;
				left: -33px;
				transform: translateY(-50%);
				content: '';
				background: #fbcd04;
				border-radius: 50%;
				z-index: 2;
				border: 6px solid #fff;
			}
			p {
				padding-left: 15px;
			}
		}
	}
}
