@use 'sass:map';
@use 'sass:meta';
@use '../base/functions' as *;

@mixin povh($povh) {
	position: relative;
	overflow: hidden;
}

@mixin heading-base() {
	line-height: 1.25;
	font-weight: 700;
	color: $heading-color;
	font-family: $font-family-secondary;
}

@mixin button-base() {
	padding: $button-padding-vertical $button-padding-horizontal;
	display: inline-block;
	// min-width: 256px;
	font-family: $font-family-secondary;
	font-size: $button-font-size;
	font-weight: 700;
	line-height: $button-line-height;
	border-radius: $button-border-radius;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	transition: $button-transition;
	appearance: none;
	outline: none;
}

@mixin button-variant($bg-color, $text-color: $button-text-color) {
	@include button-base();
	background-color: $bg-color;
	color: $text-color;
	border: 1px solid $bg-color;
	&:hover {
		background-color: hover-color($bg-color);
		border-color: hover-color($bg-color);
		color: $text-color;
	}
	&:disabled {
		opacity: $button-disabled-opacity;
	}
}

@mixin button-outline-variant($bg-color, $text-color, $border-color, $hover-bg-color: $bg-color, $hover-text-color, $hover-border-color: $hover-text-color) {
	@include button-base();
	background-color: $bg-color;
	color: $text-color;
	border: 1px solid $border-color;
	&:hover {
		background-color: $hover-bg-color;
		border-color: $hover-border-color;
		color: $hover-text-color;
	}
	&:disabled {
		opacity: $button-disabled-opacity;
	}
}

@mixin input($input) {
	height: 44px;
	padding: 8px 16px;
	border: 1px solid $border-color;
	width: 100%;
	border-radius: 8px;
	background-color: #fff;
	font-size: 16px;
	font-weight: 400;
	color: $text-color;
	font-family: $font-family;
}
@mixin input-focus($input-focus) {
	border-color: $color-primary;
}

@mixin select-wrapper($select-wrapper) {
	position: relative;
	select {
		padding-right: 32px;
		appearance: none;
	}
	&::before {
		position: absolute;
		content: '\f078';
		top: 50%;
		right: 12px;
		font-family: 'Font Awesome 5 Free';
		font-size: 12px;
		font-weight: 900;
		color: #858585;
		transform: translateY(-50%);
		z-index: 1;
	}
}

@mixin search-wrapper($search-wrapper) {
	position: relative;
	input {
		padding-left: 32px;
	}
	&::before {
		position: absolute;
		content: '\f002';
		top: 50%;
		left: 12px;
		font-family: 'Font Awesome 5 Free';
		font-size: 12px;
		font-weight: 900;
		color: #858585;
		transform: translateY(-50%);
		z-index: 1;
	}
}

// TRANSITION
@mixin transition($what) {
	& {
		transition: $what;
	}
}

@mixin hide-text {
	font: 0 / 0 a;
	text-shadow: none;
	color: transparent;
}

@mixin mq($args...) {
	$media-type: 'only screen';
	$media-type-key: 'media-type';
	$args: meta.keywords($args);
	$expr: '';

	@if map.has-key($args, $media-type-key) {
		$media-type: map.get($args, $media-type-key);
		$args: map.remove($args, $media-type-key);
	}

	@each $key, $value in $args {
		@if $value {
			$expr: '#{$expr} and (#{$key}: #{$value})';
		}
	}

	@media #{$media-type} #{$expr} {
		@content;
	}
}
