.plan-detail {
	display: flex;
	@include mq($max-width: 992px) {
		margin-bottom: 16px;
	}

	&__col {
		margin-bottom: 10px;
	}
	&__tail {
		padding: 12px 20px;
		height: 100%;
		min-height: 140px;
		position: relative;
		font-size: 16px;
		font-weight: normal;
		border-radius: 12px;
		background-color: #fff;
		user-select: none;

		@include mq($max-width: 767px) {
			min-height: auto;
		}
		&--center {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}
		&--vertical-center {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}
	&__name {
		font-size: 24px;
		font-weight: 500;
		text-wrap: pretty;
		@include mq($max-width: 767.98px) {
			font-size: 20px;
		}
	}

	&__description {
		font-size: 16px;
		@include mq($max-width: 576px) {
			font-size: 14px;
		}
		&--icon {
			position: relative;
			display: flex;
			// font awesome check icon
			&::before {
				content: '\f00c';
				font-family: 'Font Awesome 5 Free';
				font-weight: 700;
				font-size: 14px;
				color: #00c853;
				padding-right: 10px;
				line-height: 20px;
			}
		}
	}
	&__price {
		padding-bottom: 10px;
		font-size: 40px;
		font-weight: 700;
		@include mq($max-width: 767.98px) {
			font-size: 36px;
		}
		@include mq($max-width: 576px) {
			font-size: 28px;
		}
	}
	&__price-text {
		font-size: 14px;
	}
}

.plan-calculator {
	padding: 16px 20px 12px;
	position: relative;
	font-size: 16px;
	font-weight: normal;
	border-radius: 12px;
	background-color: #fff;
	user-select: none;
	&__row {
		min-height: 48px;
		margin-bottom: 16px;
		align-items: center;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__label,
	&__price {
		font-size: 20px;
		font-weight: 500;

		@include mq($min-width: 768px) {
			font-size: 24px;
		}
		&--total {
			font-weight: 700;
			font-size: 24px;
			@include mq($min-width: 768px) {
				text-align: right;
				font-size: 28px;
			}
		}
	}
	&__label {
		text-wrap: pretty;
		display: inline-block;
	}
	&__label-sub {
		font-size: 13px;
	}
	&__price {
		display: block;
		@include mq($min-width: 768px) {
			text-align: right;
		}
		@include mq($min-width: 992px) {
			text-align: center;
		}
	}
	&__tooltip {
		font-size: 18px;
		position: relative;
		@include mq($min-width: 768px) {
			font-size: 20px;
		}
		@include mq($max-width: 400px) {
			margin-right: calc(100% - 18px);
		}
		.tooltip__content {
			@include mq($max-width: 480px) {
				min-width: 120px;
			}
			@include mq($max-width: 400px) {
				min-width: 200px;
				left: 100%;
				top: 50%;
				margin-left: 8px;
				transform: translateX(0%) translateY(-50%);
				&::before {
					top: 50%;
					left: 0;
					right: unset;
					bottom: unset;
					transform: translate(-100%, -50%);
					border-width: 6px 4px 6px 0;
					border-color: transparent #3a3a3a transparent transparent;
				}
			}
		}
	}
}

.currency-selector,
.selector {
	position: relative;
	display: inline-block;
	&__select {
		appearance: none;
		margin: 0;
		padding: 8px 44px 8px 24px;
		cursor: pointer;
		border: 1px solid $color-dark;
		border-radius: 48px;
		background: #fff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		font-family: $font-family;
	}
	&__icon {
		position: absolute;
		right: 24px;
		top: 50%;
		transform: translateY(-50%);
		color: #2e2e2e;
		font-size: 14px;
		line-height: 1;
	}
}
