.pilot-section {
	background: $card-bg;
	border-radius: $card-border-radius;
	padding: 40px;
	&__title {
		margin-bottom: 12px;
	}
	&__inner {
		display: flex;
		flex-direction: column;
		gap: 32px;
	}
	&__advantages {
		gap: 24px;
		@media (min-width: 992px) {
			gap: 32px;
		}
	}
}
