@use '../base/functions' as *;

.block {
	--inverse-color: #{$color-dark};
	--text-color: #{$text-color};
	--fdd: red;
	position: relative;
	margin-bottom: 64px;
	@media (min-width: 992px) {
		margin-bottom: 120px;
	}
	&__header {
		text-align: center;
		margin-bottom: 48px;
		@media (min-width: 992px) {
			margin: 0 auto 92px;
			max-width: 832px;
		}
		p + p {
			padding-top: 12px;
		}
		&--left {
			text-align: left;
			margin-left: 0;
		}
		&--right {
			text-align: right;
			margin-right: 0;
		}
		a {
			margin-top: 24px;
		}
	}
	&__title {
		&:not(:only-child) {
			margin-bottom: 24px;
		}
	}
	&__subtitle {
		font-size: 20px;
		font-weight: 700;
		font-family: $font-family-secondary;
	}
	&--no-space {
		margin-bottom: 0;
	}
	&--dark,
	&--media,
	&--white,
	&--primary {
		--inverse-color: #{$color-dark-inverse};
		--text-color: #{$text-color-inverse};
		padding: 64px 0;
		background-color: $color-dark;
		color: $text-color-inverse;
		@media (min-width: 992px) {
			padding: 96px 0;
		}
	}
	&--primary {
		background-color: $color-primary;
	}
	&--white {
		background-color: #fff;
		color: $text-color;
	}
	&--dark &__title,
	&--dark &__subtitle,
	&--primary &__title,
	&--primary &__subtitle,
	&--media &__title,
	&--media &__subtitle {
		color: $text-color-inverse;
	}

	&--media &__header {
		position: relative;
		z-index: 1;
	}
	&__media-bg {
		position: absolute;
		inset: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		z-index: 0;
		border-radius: inherit;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.5);
		}
		img,
		video {
			position: absolute;
			inset: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
		}
	}
	&__body {
		position: relative;
	}
	&__spacing-wrapper {
		display: flex;
		flex-direction: column;
		gap: 48px;
	}
	&--sm-space {
		margin-bottom: 64px;
	}
	&--single {
		margin: 60px 0;
		@media (min-width: 992px) {
			margin: 96px 0;
		}
	}
}

.square-text {
	position: relative;
	display: grid;
	@media (min-width: 992px) {
		max-width: 992px;
		margin-left: auto;
		margin-right: auto;
	}
	&:before {
		grid-row: 1 / 1;
		grid-column: 1 / 1;
		content: '';
		display: block;
		background: #ced2fc;
		width: 240px;
		height: 240px;
		aspect-ratio: 1 / 1;
		border-radius: 20px;
		@media (min-width: 576px) {
			width: 288px;
			height: 288px;
		}
	}
	&__text {
		@extend .h2;
		padding: 32px 0 32px 32px;
		grid-row: 1 / 1;
		grid-column: 1 / 1;
		@media (min-width: 576px) {
			padding: 64px 0 32px 48px;
		}
		@media (min-width: 768px) {
			padding: 104px 0 56px 104px;
		}
	}
}

.section {
	&__grid {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		gap: $gap;
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: $gap;
	}
	&__image {
		img {
			border-radius: $border-radius;
			order: 1;
			margin-left: auto;
			margin-right: auto;
			max-width: 100%;
			height: auto;
			max-height: 400px;
			display: block;
			@media (min-width: 992px) {
				order: initial;
				max-height: none;
			}
		}
	}
	// TODO: Remove width from image and content
	&__content,
	&__image,
	&__column {
		width: 100%;
		@media (min-width: 992px) {
			width: calc(50% - $gap / 2);
		}
	}
	&--wide &__content,
	&--wide &__image {
		width: 100%;
	}
	&--wide &__grid {
		display: flex;
		flex-direction: column;
		gap: $gap;
	}
	&--reversed &__grid {
		@media (min-width: 992px) {
			flex-direction: row-reverse;
		}
	}
}

.article-section {
	&__image-col {
		order: 1;
		max-height: 400px;
		@media (min-width: 992px) {
			order: initial;
			max-height: none;
		}
	}
	&__image {
		border-radius: $border-radius;
		max-width: 100%;
		height: auto;
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-height: 100%;
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: 24px;
	}
	&__title {
		color: inherit;
	}
	&--reverse &__image-col {
		order: initial;
		@media (min-width: 992px) {
			order: 1;
		}
	}
}

.text-block {
	&__title {
		margin-bottom: 24px;
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: 16px;
	}
}

// Remove after Marina page update
.blocks-wrapper {
	padding: 60px 0;
	.block {
		&__title {
			padding-bottom: 36px;
			text-align: center;
		}
		&__sections {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
			-ms-flex-direction: column;
			flex-direction: column;
			gap: 48px;
			@media (min-width: 992px) {
				gap: 64px;
			}
		}
	}
}
