.notfound {
	padding: 40px 0;
	text-align: center;
	flex-grow: 1;
	display: flex;
	align-items: center;
	@extend .h4;
	&__img {
		margin-top: 32px;
	}
	&__text {
		margin-top: 12px;
	}
	&__contact {
		margin-top: 48px;
	}
	&__phones {
		margin-top: 24px;
		display: flex;
		flex-direction: column;
		gap: 8px;
	}
	&__phone {
		text-decoration: none;
	}
}
