.tooltip {
	position: relative;
	display: inline-block;
	flex-shrink: 0;
	&__icon {
		cursor: pointer;
		color: $link-color;
		transition: color 0.3s ease;
		&:hover {
			color: $link-color-hover;
		}
	}
	&__content {
		position: absolute;
		left: 50%;
		top: -6px; // Default position is at the top
		transform: translateX(-50%) translateY(-100%);
		background: #3a3a3a;
		text-align: center;
		color: #fff;
		padding: 6px 4px;
		font-size: 0.75rem;
		line-height: 1.25;
		min-width: 240px;
		border-radius: 5px;
		pointer-events: none;
		z-index: 99;
		opacity: 0;
		transition: opacity 0.3s;

		&::before {
			content: '';
			position: absolute;
			top: unset;
			left: 50%;
			right: unset;
			bottom: 0px;
			transform: translateX(-50%) translateY(100%);
			border-width: 4px 6px 0 6px;
			border-style: solid;
			border-color: #3a3a3a transparent transparent transparent;
			z-index: 99;
			opacity: 0;
			transition: opacity 0.3s;
		}
	}

	&__icon:hover + &__content,
	&__icon:hover + &__content::before {
		opacity: 1;
	}

	&--top {
		.tooltip__content {
			left: 50%;
			margin-bottom: 8px;
			transform: translateX(-50%) translateY(-100%);
			&::before {
				top: unset;
				left: 50%;
				right: unset;
				bottom: 0px;
				transform: translateX(-50%) translateY(100%);
				border-width: 4px 6px 0 6px;
				border-color: #3a3a3a transparent transparent transparent;
			}
		}
	}

	&--right {
		.tooltip__content {
			left: 100%;
			top: 50%;
			margin-left: 8px;
			transform: translateX(0%) translateY(-50%);
			&::before {
				top: 50%;
				left: 0;
				right: unset;
				bottom: unset;
				transform: translate(-100%, -50%);
				border-width: 6px 4px 6px 0;
				border-color: transparent #3a3a3a transparent transparent;
			}
		}
	}
	&--bottom {
		.tooltip__content {
			top: 100%;
			margin-top: 8px;
			transform: translateX(-50%) translateY(0%);
			&::before {
				top: 0;
				left: 50%;
				right: unset;
				bottom: unset;
				transform: translateX(-50%) translateY(-100%);
				border-width: 0 6px 4px 6px;
				border-color: transparent transparent #3a3a3a transparent;
			}
		}
	}
	&--left {
		.tooltip__content {
			left: 0;
			top: 50%;
			transform: translateX(-100%) translateY(-50%);
			margin-right: 8px;
			&::before {
				top: 50%;
				left: unset;
				right: 0px;
				bottom: unset;
				transform: translate(100%, -50%);
				border-width: 6px 0px 6px 4px;
				border-color: transparent transparent transparent #3a3a3a;
			}
		}
	}
}
