.check-email {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	gap: 20px;
	padding: 60px 0;
	@media (min-width: 992px) {
		padding: 96px 0;
	}
	img {
		width: 136px;
	}

	&__gmail-button {
		margin-top: 24px;
		display: flex;
		align-items: center;
		gap: 12px;
		img {
			width: 24px;
		}
	}
	&__resend-link {
		margin-top: 80px;
		font-size: 14px;
	}
}
