.ukpms-hero {
	background: url(../img/ukpms-hero-clouds.png) repeat-x bottom, url(../img/ukpms-hero-bg.jpg) no-repeat center;
	background-size: auto, cover;
	padding-top: 140px;
	padding-bottom: 255px;
	@include mq($max-width: 991px) {
		text-align: center;
	}
	@include mq($max-width: 575px) {
		padding-top: 80px;
		padding-bottom: 150px;
		text-align: center;
	}
	h1 {
		padding-bottom: 25px;
		font-size: 55px;
		font-weight: normal;
		line-height: normal;
		letter-spacing: 0.55px;
		color: #2b54a1;
		@include mq($max-width: 767px) {
			font-size: 36px;
		}
	}
	h2 {
		padding-right: 20px;
		padding-bottom: 30px;
		font-size: 30px;
		font-weight: 500;
		line-height: normal;
		letter-spacing: 0.75px;
		color: #3f3f3f;
		@include mq($max-width: 991px) {
