// Slider #1
.property-slider {
	position: relative;
	margin-bottom: 15px;
	&__slide {
		padding-top: 70px;
		padding-bottom: 20px;
		position: relative;
	}
	.slide__image {
		position: relative;
		transition: all 0.4s ease;
		&::after {
			display: block;
			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			background-color: rgba(255, 255, 255, 0.73);
			top: 0;
			left: 0;
		}
		img {
			max-width: 100%;
		}
	}
	.slide__title-container {
		display: none;
		margin: 30px auto 0;
		min-height: 85px;
		position: relative;
		z-index: 2;
	}
	.slide__title {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		padding: 0;
		width: 330px;
		min-height: 85px;
		display: flex;
		align-items: center;
		justify-content: center;
		box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.07);
		background-color: #fff;
		border-radius: 5px;
		font-size: 30px;
		font-weight: bold;
		line-height: 1;
		text-align: center;
		letter-spacing: 0.75px;
		color: #3e63f5;
		text-decoration: none;
		transition: all 0.3s ease;
		&:hover {
			box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
			color: #f79c6a;
		}
	}

	// Active slide (Swiper replacement for Slick current)
	.swiper-slide.swiper-slide-active {
		z-index: 2;
		.slide__title-container {
			display: block;
		}
		.slide__button {
			display: block;
		}
		.slide__image {
			z-index: 1;
			@include mq($min-width: 768px) {
				transform: scale(1.5);
			}
			&::after {
				display: none;
			}
		}
	}

	.swiper-arrow {
		position: absolute;
		top: 160px;
		z-index: 10;
		cursor: pointer;
		left: 0;
		width: 56px;
		height: 56px;
		border-radius: 50%;
		background: #fff url(../img/icons/arrow.png) no-repeat 55% center;
		box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.05);
		transition: all 0.3s ease;
		@include hide-text;
		@include mq($max-width: 991px) {
			top: 30%;
		}
		@include mq($max-width: 767px) {
			top: 35%;
		}
		&--prev {
			transform: scaleX(-1);
		}
		&--next {
			left: auto;
			right: 0px;
		}
		&:hover {
			opacity: 0.9;
		}
		&::after {
			display: none;
		}
	}
}

