.comparison {
	@media (min-width: 576px) {
		table-layout: fixed;
	}
	.selected {
		position: relative;
		@media (min-width: 768px) {
			color: #fff;
			background-color: $color-dark;
		}
		&::before {
			position: absolute;
			top: 0;
			left: 12px;
			right: 12px;
			bottom: 0;
			background: #444;
			pointer-events: none;
			@media (min-width: 768px) {
				content: '';
			}
		}
		& * {
			position: relative;
		}
	}
	thead {
		th,
		td {
			padding: 16px 4px;
			span {
				max-width: 240px;
				display: inline-block;
			}
			@media (min-width: 768px) {
				padding: 24px 16px 40px;
			}
		}
		.selected {
			@media (min-width: 768px) {
				border-radius: 16px 16px 0 0;
				&::before {
					border-radius: 16px 16px 0 0;
					top: calc(100% - 16px);
				}
			}
		}
	}
	&__logo {
		display: block;
		margin: 0 auto;
		max-width: 240px;
		width: 100%;
	}
	&__logo--desktop {
		display: block;
		@media (max-width: 768px) {
			display: none;
		}
	}
	&__logo--mobile {
		display: none;
		max-width: 48px;
		@media (max-width: 768px) {
			display: block;
		}
	}

	tbody {
		th,
		td {
			padding: 12px 4px;
			border-bottom: 1px solid $strokes;
			@media (max-width: 768px) {
				padding: 12px 4px;
			}
		}
		th {
			text-align: left;
		}
		td {
			line-height: 1;
		}
		.selected {
			@media (min-width: 768px) {
				padding: 12px 16px;
			}
		}
	}
	tfoot {
		.selected {
			@media (min-width: 768px) {
				height: 40px;
				border-radius: 0 0 16px 16px;
				&::before {
					border-radius: 0 0 16px 16px;
					bottom: 16px;
				}
			}
		}
	}
	th,
	td {
		min-width: 56px;
		vertical-align: middle;
		font-size: 14px;
		font-weight: 400;
		text-align: center;
		@media (min-width: 576px) {
			font-size: 16px;
		}
	}
	th {
		font-size: 14px;
		@media (min-width: 576px) {
			font-size: 16px;
		}
		@media (min-width: 768px) {
			font-size: 18px;
		}
	}
}
