.check-before {
	&::before {
		display: block;
		flex: 0 0 auto;
		content: '';
		background: url(../img/icons/check-circle.svg) no-repeat center center / contain;
		width: 20px;
		height: 1.5rem;
	}
}

.list {
	line-height: 1.5;
	li {
		position: relative;
		padding-bottom: 16px;
		display: flex;
		gap: $gap-list-icon;
	}
	&--dot,
	&--dot-mini {
		li {
			&::before {
				position: relative;
				top: 4px;
				display: block;
				flex: 0 0 auto;
				width: 12px;
				height: 12px;
				content: '';
				background-color: $color-primary;
				border-radius: 50%;
			}
		}
	}
	&--dot-mini li::before {
		top: 9px;
		width: 6px;
		height: 6px;
	}
	&--circle-check {
		li {
			@extend .check-before;
		}
	}
	&--numbered {
		li {
			counter-increment: list-counter;
			&::before {
				display: block;
				flex: 0 0 auto;
				content: counter(list-counter);
				font-weight: 700;
			}
		}
	}
}

.circled-list {
	display: flex;
	flex-direction: column;
	gap: var(--list-gap, 12px);
	li {
		display: flex;
		column-gap: 8px;
		&::before {
			content: '';
			display: block;
			flex: 0 0 auto;
			border-radius: 50%;
			width: 20px;
			height: 20px;
			border: 6px solid $color-primary;
		}
		ul {
			width: 100%;
		}
	}
}

.rtl {
	.circled-list {
		padding-left: 0;
		padding-right: 36px;
		&::before {
			left: unset;
			right: 0;
		}
	}
}

.base-list {
	display: flex;
	flex-direction: column;
	gap: var(--list-gap, 12px);
	li {
		display: flex;
		gap: var(--list-icon-gap, 12px);

		&::before {
			display: block;
			flex: 0 0 auto;
			width: 18px;
			height: 18px;
		}
	}
	&--circle-check li {
		&::before {
			content: url(../img/icons/base-circle-check.svg);
		}
	}
	&--cancel li {
		&::before {
			content: url(../img/icons/base-cancel.svg);
		}
	}
}

.list-cols {
	&--2 {
		gap: 32px;
		@media (min-width: 768px) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
		}
		@media (min-width: 1200px) {
			gap: 36px 12%;
		}
	}
}

.links-list {
	&--cols-2 {
		@media (min-width: 992px) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			gap: 0 36px;
		}
		@media (min-width: 1200px) {
			gap: 0 72px;
		}
	}
}

.badge-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 16px;
	@media (min-width: 992px) {
		gap: 24px;
	}
	li {
		padding: 12px 16px;
		border-radius: 20px;
		font-family: $font-family-secondary;
		font-weight: 700;
		font-size: 18px;
		line-height: 1.2;
		text-transform: capitalize;
		background: #fff;
		@media (min-width: 992px) {
			padding: 24px;
			font-size: 24px;
		}
		&:nth-child(9n + 1) {
			background: #e1f3fc;
		}
		&:nth-child(9n + 3) {
			background: #f8e1fc;
		}
		&:nth-child(9n + 5) {
			background: #e1fcf6;
		}
	}
}

.ol-default {
	li {
		margin-left: 28px;
		list-style-type: disc;
		&:not(:last-child) {
			margin-bottom: var(--list-gap, 12px);
		}
	}
	&--circle li {
		list-style-type: circle;
	}
	&--square li {
		list-style-type: square;
	}
}
