.schedule-wrapper {
	padding-top: 60px;
	padding-bottom: 60px;
	overflow: hidden;
	display: grid;
	gap: 48px;

	@media (min-width: 992px) {
		padding-top: 90px;
		padding-bottom: 90px;
		text-align: left;
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: auto 1fr;
		&__info {
			grid-column: 1;
			grid-row: 1;
		}
		&__form {
			grid-column: 2;
			grid-row: 1 / -1;
		}
		&__badges {
			grid-column: 1;
			grid-row: 2;
		}
	}
	&__badges {
		display: flex;
		align-items: start;
		gap: 24px;
		justify-content: center;
		@media (min-width: 992px) {
			justify-content: start;
		}
		img {
			width: 110px;
			height: auto;
		}
	}
}

.schedule-testimonials {
	margin-top: 36px;
}

.schedule-card {
	overflow: hidden;
	padding: 40px;
	border-radius: 24px;
	background-color: #fff;
	text-align: center;
	&__title {
		margin-bottom: 16px;
	}
}

.calendly-widget {
	margin-left: auto;
	margin-right: auto;
	height: 600px;
}

.schedule-link {
	margin: 32px auto 0;
	display: inline-block;
	color: $text-color;
}
