.faq-wrapper {
	display: flex;
	flex-direction: column;
	gap: 48px;
	@media (min-width: 992px) {
		flex-direction: row;
	}
	@media (min-width: 1200px) {
		gap: 120px;
	}
	&__controls {
		flex: 1 1 40%;
	}

	&__content {
		flex: 1 1 40%;
	}
}

.faq-controls {
	@media (min-width: 992px) {
		position: sticky;
		top: 100px;
	}
	&__categories {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		gap: 8px;
		@media (min-width: 992px) {
			gap: 16px;
		}
	}
	&__search {
		margin-bottom: 28px;
		@include search-wrapper('');
		@media (min-width: 768px) {
			margin-bottom: 40px;
		}
	}
}

.faq-category {
	border-radius: 48px;
	padding: 8px 16px;
	color: $heading-color;
	border: 1px solid $color-dark;
	text-decoration: none;
	display: inline-block;
	@media (min-width: 768px) {
		padding: 8px 28px;
	}
	&.active {
		background-color: $color-dark;
		color: $text-color-inverse;
	}
	&:hover:not(.active),
	&:focus:not(.active) {
		background-color: #fff;
		color: $heading-color;
	}
}

.faq-cta {
	margin-top: 32px;
	border-radius: 20px;
	padding: 24px;
	background-color: $card-bg;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 12px;
	a {
		margin-top: 48px;
	}
}

.faq-not-found {
	font-size: 18px;
	font-weight: 500;
	display: none;
}
