.row .gutters-small > [class*='col'] {
	--row-gutter: 20px;
}

.bump-left {
	margin-right: auto;
}

.bump-right {
	margin-left: auto;
}

.card-grid {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 0 -16px;
	row-gap: 32px;

	&__item {
		flex: 0 1 auto;
		padding: 0 16px;
	}

	&--cols-2 &__item {
		width: 50%;
	}
	&--cols-3 &__item {
		width: 33.3333%;
	}
	&--cols-4 &__item {
		width: 25%;
	}
	&--cols-5 &__item {
		width: 20%;
	}

	@media (max-width: 992px) {
		&--cols-2 &__item {
			width: 50%;
		}
		&--cols-3 &__item {
			width: 33.3333%;
		}
		&--cols-4 &__item {
			width: 33.3333%;
		}
		&--cols-5 &__item {
			width: 33.3333%;
		}
	}

	@media (max-width: 768px) {
		&--cols-2 &__item {
			width: 100%;
		}
		&--cols-3 &__item {
			width: 50%;
		}
		&--cols-4 &__item {
			width: 50%;
		}
		&--cols-5 &__item {
			width: 50%;
		}
	}

	@media (max-width: 576px) {
		&--cols-2 &__item,
		&--cols-3 &__item,
		&--cols-4 &__item,
		&--cols-5 &__item {
			width: 100%;
		}
	}
}
