.subscribe-form {
	padding: 2px;
	background: #fff;
	position: relative;
	overflow: hidden;
	display: flex;
	margin-bottom: 12px;
	border-radius: $border-radius;
	border: 1px solid $strokes;
	height: 52px;
	input:not(.button) {
		width: 100%;
		height: 100%;
		border: none;
		background: none;
		box-shadow: none !important;
		flex: 1;
		padding: 0 24px;
		color: #545353;
		font-size: 14px;
		font-weight: 300;
		border-radius: $border-radius;
		@media (min-width: 576px) {
			font-size: 18px;
		}
	}
	.button {
		min-width: auto;
		border-radius: $border-radius;
	}
}

.form,
.sign-up-form {
	border-radius: 20px;
	padding: 40px;
	background: #fff;
	&__title {
		@extend .h3;
		margin-bottom: 16px;
		text-align: center;
	}
	&__subtitle {
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		text-align: center;
	}
	&__form {
		margin-top: 32px;
		display: flex;
		flex-direction: column;
		gap: 24px;
	}
	&__separator {
		margin: 32px 0;
		display: flex;
		align-items: center;
		gap: 16px;
		color: $strokes;
		&::before,
		&::after {
			content: '';
			flex-grow: 1;
			height: 1px;
			background: $strokes;
		}
	}
	&__select-wrapper {
		@include select-wrapper('');
	}
	&__error {
		padding-top: 2px;
		color: $color-danger;
		font-size: 12px;
		display: none;
	}
}
