.core-value-card {
	border-radius: 20px;
	padding: 24px;
	background-color: $card-bg;
	display: flex;
	flex-direction: column;
	height: 100%;
	img {
		margin: 0 auto 32px;
		width: 120px;
		display: block;
		@media (min-width: 768px) {
			width: 164px;
		}
	}
	&__title {
		margin-bottom: 8px;
	}
}
