.interactive-list {
	position: relative;
	display: flex;
	flex-direction: column;
	gap: 16px;
	@media (min-width: 768px) {
		flex-direction: row;
		gap: 24px;
	}
	@media (min-width: 992px) {
		gap: 72px;
	}
	&__control {
		flex-grow: 1;
	}
	&__list {
		li.active + li {
			border-top: none;
		}
		li {
			border-top: 1px solid $strokes;
			&:last-child {
				border-bottom: 1px solid $strokes;
			}
		}
	}
	&__content {
		flex-shrink: 0;
		width: 100%;
		@media (min-width: 768px) {
			width: 50%;
		}
	}
	&__all {
		padding: 24px;
		text-align: center;
	}
	&__content-card {
		display: none;
		border-radius: 24px;
		padding: 20px;
		backdrop-filter: blur(3px);
		box-shadow: inset 2px 2px 1px 0 rgba(255, 255, 255, 0.5), inset -1px -1px 1px 1px rgba(255, 255, 255, 0.5);
		background-color: rgba(0, 0, 0, 0.01);
		background-blend-mode: lighten;
		&.active {
			display: block;
		}
		img,
		video {
			width: 100%;
			background-color: #fff;
			border-radius: 16px;
			display: block;
		}
	}
}

.interactive-card {
	&__trigger {
		padding: 24px;
		cursor: pointer;
		-webkit-tap-highlight-color: transparent;
		-webkit-focus-ring-color: rgba(255, 255, 255, 0);
	}
	&__content {
		display: none;
		padding-top: 12px;
		a {
			margin-top: 24px;
		}
	}

	&.active {
		padding: 24px;
		background: $color-dark;
		color: #fff;
		border-radius: 16px;
	}
	.active &__trigger {
		padding: 0;
		cursor: default;
		pointer-events: none;
	}
	.active &__content {
		display: block;
	}
	.active &__title {
		color: #fff !important;
	}
}
