@use '../base/functions' as *;

.button {
	@include button-variant($button-primary-bg);

	&-secondary {
		@include button-variant($button-secondary-bg);
	}

	&-whatsapp {
		@include button-variant($button-whatsapp-bg);
	}

	&-success {
		@include button-variant($button-success-bg);
	}

	&-danger {
		@include button-variant($button-danger-bg);
	}

	&-warning {
		@include button-variant($button-warning-bg);
	}

	&-outline {
		@include button-outline-variant(
			$button-outline-bg,
			$button-outline-text-color,
			$button-outline-border-color,
			$button-outline-bg,
			$button-outline-hover-color
		);
	}

	&-outline-white {
		@include button-outline-variant(transparent, #fff, #fff, #fff, $button-outline-text-color, #fff);
	}

	&-inverse {
		@include button-variant($button-inverse-bg, $button-inverse-text-color);
	}

	&--auto {
		min-width: auto;
	}

	&--stretch {
		width: 100%;
	}
}

