// Fixed whatsapp button
.whatsapp-button {
	background: $button-whatsapp-bg;
	padding: 10px 14px;
	border-radius: 40px;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	color: #fff;
	text-decoration: none;
	cursor: pointer;
	position: fixed;
	z-index: 999999;
	bottom: 20px;
	right: 20px;
	transition: $button-transition;
	@include mq($max-width: 991px) {
		display: none;
	}
	&:hover {
		color: #fff;
		background-color: hover-color($button-whatsapp-bg);
	}
	svg {
		margin-right: 8px;
	}
	&__mobile {
		display: none;
		@include mq($max-width: 991px) {
			display: inline-flex;
		}
	}
}

.swiper-arrow {
	position: absolute;
	top: calc(50% - var(--button-size, 48px) / 2);
	z-index: 10;
	cursor: pointer;
	width: var(--button-size, 48px);
	height: var(--button-size, 48px);
	border-radius: 50%;
	background: #fff url(../img/icons/arrow.png) no-repeat 55% center;
	box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
	transition: all 0.3s ease;
	-webkit-tap-highlight-color: transparent;
	transform: scale(1);
	transition: transform 0.3s ease;
	@include hide-text;
	&--prev {
		transform: scaleX(-1);
		transform-origin: center;
		left: 0;
	}
	&--next {
		right: 0px;
	}
	&.swiper-button-disabled {
		transform: scale(0);
	}
}

.swiper-base-arrow {
	width: 64px;
	height: 64px;
	border-radius: 50%;
	border: 1px solid var(--inverse-color, $color-dark);
	z-index: 10;
	cursor: pointer;
	-webkit-tap-highlight-color: transparent;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	&:before {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		width: calc(100% - 10px);
		height: calc(100% - 10px);
		border-radius: 50%;
		transform: translate(-50%, -50%);
		transition: transform 0.3s ease, background 0.3s ease;
		z-index: -1;
	}
	&.swiper-button-disabled {
		border-color: $strokes;
		svg {
			stroke: $strokes;
		}
	}
	&:hover:not(.swiper-button-disabled) {
		&::before {
			background: #ced2fc;
		}
		svg {
			stroke: $color-dark;
		}
	}
	svg {
		stroke: var(--inverse-color, $color-dark);
	}
}

.arrow-link {
	color: $color-primary;
	font-family: $font-family-secondary;
	font-weight: 700;
	font-size: 18px;
	text-decoration: none;
	@media (min-width: 768px) {
		font-size: 20px;
	}
	i {
		margin-left: 8px;
	}
}
