@use '../base/functions' as *;

.tabs {
	padding: 4px;
	border: 1px solid $color-dark;
	border-radius: 48px;
	display: inline-flex;
	gap: 8px;
	color: $color-dark;

	&__button {
		height: 100%;
		padding: 8px 24px;
		min-height: 42px;
		display: flex;
		align-items: center;
		justify-items: center;
		background-color: transparent;
		border-radius: 48px;
		font-family: $font-family-secondary;
		font-weight: 700;
		font-size: 18px;
		transition: all 0.3s ease;
		user-select: none;
		-webkit-tap-highlight-color: transparent;
		@media (min-width: 576px) {
			padding: 8px 32px;
			font-size: 20px;
		}
		&:hover:not(.active) {
			background-color: color-opacity($color-dark, 0.1);
		}
		&.active {
			background-color: $color-dark;
			color: $color-dark-inverse;
		}
	}
}
