.hero {
	padding-top: 60px;
	padding-bottom: 60px;
	text-align: center;
	font-size: 18px;
	@media (min-width: 992px) {
		padding-top: 90px;
		padding-bottom: 90px;
		text-align: left;
	}
	@media (min-width: 992px) {
		&__grid--g-xl {
			--bs-gutter-x: 60px;
		}
		&__grid--g-lg {
			--bs-gutter-x: 48px;
		}
		&__grid--g-md {
			--bs-gutter-x: 32px;
		}
		&__grid--g-sm {
			--bs-gutter-x: 24px;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		gap: 24px;
	}
	&__text {
		font-size: 18px;
		line-height: 24px;
	}
	&__img,
	&__video {
		margin-top: 48px;
		max-width: 100%;
		height: auto;
		border-radius: $border-radius;
		@media (min-width: 992px) {
			margin-top: 0;
		}
	}
	&__video {
		width: 100%;
	}
	&__buttons {
		margin-top: 24px;
		display: flex;
		flex-wrap: wrap;
		gap: $gap;
		@media (max-width: 991.98px) {
			justify-content: center;
		}
	}
}

.hero-full {
	position: relative;
	margin-top: 60px;
	margin-bottom: 60px;
	font-size: 18px;
	color: $text-color-inverse;
	font-family: $font-family-secondary;
	@media (min-width: 992px) {
		margin-bottom: 90px;
	}
	&__wrapper {
		padding: 40px;
		position: relative;
		min-height: 400px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		border-radius: 20px;
		background-color: $color-dark;
		@media (min-width: 992px) {
			padding: 72px;
			min-height: 600px;
		}
	}
	&__media-bg {
		position: absolute;
		inset: 0;
		width: 100%;
		height: 100%;
		border-radius: 20px;
		overflow: hidden;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.5);
		}
		img,
		video {
			position: absolute;
			inset: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
		}
	}
	&__content {
		@extend .hero__content;
		position: relative;
	}
	h1,
	h2,
	h3,
	p {
		color: $text-color-inverse;
	}
	&__text {
		@extend .hero__text;
	}
	&__buttons {
		@extend .hero__buttons;
		justify-content: flex-start;
	}
	&--center &__content {
		text-align: center;
		align-items: center;
	}
	&--center &__buttons {
		justify-content: center;
	}
}

.hero-base {
	margin-top: 60px;
	margin-bottom: 60px;
	text-align: center;
	@media (min-width: 992px) {
		max-width: 992px;
		margin-left: auto;
		margin-right: auto;
	}
	&--fluid {
		max-width: initial;
	}
	&__content {
		@extend .hero__content;
		position: relative;
		align-items: center;
	}
	&__buttons {
		@extend .hero__buttons;
		justify-content: center;
	}
	&__media {
		margin: 24px auto 0;
		padding: 20px;
		max-width: 896px;
		backdrop-filter: blur(3px);
		box-shadow: inset 2px 2px 1px 0 rgba(255, 255, 255, 0.5), inset -1px -1px 1px 1px rgba(255, 255, 255, 0.5);
		background-color: rgba(0, 0, 0, 0.01);
		background-blend-mode: lighten;
		border-radius: $border-radius;
		img,
		video {
			max-width: 100%;
			width: 100%;
			background-color: #fff;
			border-radius: 16px;
			display: block;
		}
	}
	&__text {
		@extend .hero__text;
	}
}

.hero-card {
	margin-top: 60px;
	margin-bottom: 60px;
	padding: 40px;
	background-color: $card-bg;
	border-radius: 20px;
	color: $color-primary;
	@media (min-width: 992px) {
		padding: 72px;
		margin-bottom: 90px;
	}
	&__content {
		@extend .hero__content;
	}
	&__title {
		color: $color-primary;
	}
	&__text {
		color: $color-primary;
		font-size: 18px;
		line-height: 24px;
		@media (min-width: 992px) {
			font-size: 20px;
			line-height: 28px;
		}
	}
}

.hero-text {
	margin-top: 60px;
	margin-bottom: 60px;
	@media (min-width: 992px) {
		margin-top: 90px;
		margin-bottom: 90px;
	}
	&__content {
		@extend .hero__content;
	}
	&__text {
		font-size: 18px;
		line-height: 24px;
		@media (min-width: 992px) {
			font-size: 20px;
			line-height: 28px;
		}
	}
}
