.slider-outside {
	overflow: hidden;
	box-sizing: border-box;
	.swiper {
		position: relative;
		overflow: visible;
		margin-bottom: 90px;
		@media (min-width: 480px) {
			margin-bottom: 0;
		}
		.swiper-arrow--prev {
			left: -8px;
		}
		.swiper-arrow--next {
			right: -8px;
		}
		&-wrapper {
			box-sizing: border-box;
		}
		.swiper-slide {
			transition: opacity 0.3s ease;
			height: auto;
		}
	}
	.swiper-slide {
		width: auto;
	}
	&__actions {
		position: relative;
		margin-top: 32px;
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
	}
	&__navigation {
		width: 100%;
		display: flex;
		gap: 20px;
		margin-top: 24px;
		justify-content: center;
		@media (min-width: 768px) {
			justify-content: flex-start;
			margin-top: 0;
		}
		&:has(.swiper-button-disabled + .swiper-button-disabled) {
			display: none;
		}
	}
	&__pagination {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		gap: 20px 12px;
		@media (min-width: 768px) {
			--swiper-pagination-top: 0;
			--swiper-pagination-bottom: auto;
			top: 0;
			padding: 0 160px;
			width: 100%;
			min-height: 64px;
			position: absolute;
		}
	}
	&__bullet {
		width: 32px;
		height: 4px;
		background-color: #96949a;
		cursor: pointer;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: calc(100% + 8px);
			height: calc(100% + 8px);
			border-radius: 2px;
			transition: all 0.3s ease;
		}
		@media (min-width: 768px) {
			width: 70px;
		}
		&--active {
			background-color: var(--inverse-color, #333333);
		}
	}
	&__link {
		margin-top: 32px;
		margin-left: auto;
		display: inline-block;
		@media (min-width: 992px) {
			margin-top: 16px;
		}
	}
}

.animated-line {
	position: relative;
	overflow: hidden;
	&__track {
		display: flex;
		align-items: center;
		width: fit-content;
		animation: scroll 40s linear infinite;
		will-change: transform;
	}
	&__group {
		display: flex;
		align-items: center;
		flex: 0 0 50%;
		width: 200%;
		img {
			display: block;
			padding-right: 76px;
			max-width: none;
		}
	}
}
@keyframes scroll {
	from {
		transform: translateX(0);
	}
	to {
		transform: translateX(-50%);
	}
}
