// Normally mainpage media styles
// You can read about usage in https://github.com/paranoida/sass-mediaqueries

@include mq($max-width: 1100px) {
	.contact_img {
		display: none;
	}
	.contact_side {
		float: none;
		margin: 0 auto;
		padding-bottom: 20px;
	}
}
@include mq($max-width: 1000px) {
	.gdpr-bottom__block {
		display: block;
		figure {
			display: block;
			max-width: 100%;
			text-align: center;
			br {
				display: none;
			}
		}
	}
}
@include mq($max-width: 992px) {
	.data-top {
		background: #01a4e4;
	}
}

@include mq($max-width: 790px) {
	.customer-top__text {
		display: none;
		&.active {
			display: block;
		}
	}
	.customer-top__left,
	.customer-top__right {
		width: 100%;
		max-width: 100%;
	}
	.customer-top__text {
		padding-top: 40px;
	}
	.customer-tags__block {
		width: 100%;
	}
}

@include mq($max-width: 760px) {
	.customer-top {
		background: none;
	}
	.customer-top__text h2,
	.customer-top__text h5 {
		color: #262626;
	}
	.gdpr-top .container {
		display: block;
		dl {
			width: 100%;
			margin-bottom: 30px;
		}
	}
	.gdpr-bottom__block {
		padding-top: 0;
	}
	.data-top h2 {
		font-size: 28px;
		text-align: center;
	}
	.data-top blockquote {
		margin-left: 0;
		font-size: 20px;
	}
	.data-top h6 {
		font-size: 24px;
		text-align: center;
	}
}

@include mq($max-width: 550px) {
	.advantage_block {
		flex-wrap: wrap;
		figure {
			width: 47%;
		}
	}
	.contact_side {
		width: 100%;
	}
	.contact_form label {
		display: block;
		padding: 0 0 15px 0 !important;
		text-align: center;
	}
	.contact_form small {
		text-align: center;
	}
	.gdpr-center__img {
		min-width: 70px;
		padding: 15px;
	}
}

@include mq($max-width: 450px) {
	.advantage_block {
		figure {
			width: 100%;
		}
	}
}
