@use '../base/functions' as *;

.pricing-plans {
	padding-bottom: 30px;
	line-height: normal;
	background-color: #eef1f7;
	@include mq($max-width: 991px) {
		padding-bottom: 15px;
	}
	@include mq($max-width: 991px) {
		padding-bottom: 0px;
	}
}
.subscription-length {
	text-align: center;
	margin-bottom: 64px;
	font-size: 14px;
	&__tabs {
		margin-bottom: 16px;
		button {
			padding: 8px 12px;
			font-size: 18px;
		}
	}
	&__period {
		margin: 16px 0;
		display: flex;
		justify-content: center;
		display: none;
	}
}

.pricing-card {
	padding: 20px 16px;
	background-color: #fff;
	border-radius: 20px;
	&__title {
		@extend .h2;
		margin-bottom: 12px;
	}
	@include mq($min-width: 576px) {
		padding: 20px 24px;
	}
}

.pricing-size {
	display: flex;
	flex-direction: column;
	gap: 8px;
	&__item {
		position: relative;
		padding: 16px 32px 16px 16px;
		cursor: pointer;
		background-color: #fff;
		border-radius: 20px;
		user-select: none;
		transition: all 0.3s ease;
		&:hover:not(.active) {
			background-color: color-opacity($color-dark, 0.1);
		}
		&.active {
			background-color: $color-dark;
			color: $color-dark-inverse;
		}
	}
	&__icon {
		position: absolute;
		right: 12px;
		top: 50%;
		transform: translateY(-50%);
		font-size: 16px;
		color: #96949a;
		transition: all 0.3s ease;
	}
	&__name {
		@extend .h4;
		padding-bottom: 4px;
		color: inherit;
	}
	&__amount {
		font-size: 16px;
		line-height: 1.5;
	}
	&.active &__amount {
		color: $color-primary-light;
	}
}

.pricing-plan {
	display: flex;
	flex-direction: column;
	gap: 40px;
	padding: 20px;
	background: #fff;
	text-align: center;
	@include mq($min-width: 992px) {
		padding: 40px;
	}
	&__subscription-price {
		@extend .h4;
		color: $strokes;
		display: inline-flex;
		flex-wrap: wrap;
		align-items: flex-end;
		justify-content: center;
		gap: 4px;
	}
	&__subscription-price-wrap {
		display: inline-flex;
		flex-wrap: wrap;
		align-items: flex-end;
		gap: 4px;
	}
	&__price {
		color: #202020;
		font-weight: 900;
		font-size: 32px;
		@include mq($min-width: 768px) {
			font-size: 56px;
		}
		@include mq($min-width: 992px) {
			font-size: 60px;
		}
		@include mq($min-width: 1200px) {
			font-size: 76px;
		}
	}
	&__subscription-price &__currency-symbol,
	&__subscription-price &__period {
		display: inline-block;
		line-height: 40px;
		@include mq($min-width: 768px) {
			line-height: 72px;
		}
	}
	&__description {
		padding: 12px 24px;
		border-radius: 8px;
		display: flex;
		gap: 16px;
		background: $color-primary-light;
		text-align: left;
		align-items: flex-start;
		font-size: 14px;
		img {
			flex: 0 0 auto;
		}
	}
	&__features {
		row-gap: 24px;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		text-align: left;
		@include mq($max-width: 767.98px) {
			display: flex;
			row-gap: 16px;
		}
	}
	&__buttons {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 12px;
		i {
			font-size: 14px;
			padding-left: 4px;
		}
	}
}

// Outdated CSS
.pricing-section {
	padding-top: 40px;
	padding-bottom: 40px;
	background: #eef1f7;
	color: #333333;
	@include mq($max-width: 767px) {
		padding-top: 30px;
		padding-bottom: 30px;
	}
	.pricing-section__title {
		padding-bottom: 40px;
		font-size: 42px;
		font-weight: 500;
		color: #303030;
		line-height: normal;
		@include mq($max-width: 767px) {
			font-size: 32px;
			padding-bottom: 30px;
		}
		&--inverse {
			color: #fff;
		}
	}
}
.pricing-filters {
	margin-bottom: 20px;
	&__info {
		margin-bottom: 20px;
		font-size: 18px;
		font-style: italic;
		svg {
			margin-left: 5px;
			display: inline;
			line-height: 1;
			vertical-align: -5px;
		}
	}
	&__popup-btn {
		text-decoration: none;
	}
}
.pricing-support {
	@include mq($max-width: 767px) {
		font-size: 14px;
	}
	&__btn {
		margin-top: 15px;
	}
	&__item {
		margin-bottom: 25px;
		padding: 30px 15px;
		height: calc(100% - 25px);
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		gap: 30px;
		align-self: stretch;
		font-size: 14px;
		color: #3a3a3a;
		border-radius: 12px;
		background: #fff;
		h3 {
			font-size: 24px;
			font-weight: 700;
			margin-bottom: 20px;
			@include mq($max-width: 767px) {
				font-size: 18px;
			}
		}
	}

	&__icon {
		max-width: 60px;
		max-height: 60px;
	}
}
.pricing-addons {
	overflow: hidden; // contain horizontal overflow within the section
	&__wrapper {
		padding-top: 40px;
		padding-bottom: 40px;
		background: #305e93;
		@include mq($max-width: 767px) {
			padding-top: 30px;
			padding-bottom: 30px;
		}
	}
	&__wrapper &__slider {
		// Left ghost slide
		.swiper-slide.previous-slide {
			opacity: 30%;
			visibility: visible;
			position: relative;
			filter: blur(5px);
			&:before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				background-image: linear-gradient(to left, transparent, #305e93);
			}
		}
		// Right ghost slide
		.swiper-slide.next-slide {
			opacity: 30%;
			visibility: visible;
			position: relative;
			filter: blur(5px);
			&:before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				background-image: linear-gradient(to right, transparent, #305e93);
			}
		}
	}
	&__slider {
		&.swiper {
			width: 100%;
			overflow: visible; // show ghost slides
		}
		// Custom navigation buttons (no Swiper default classes)
		.swiper-arrow {
			position: absolute;
			top: calc(50% - 28px);
			z-index: 10;
			cursor: pointer;
			width: 56px;
			height: 56px;
			border-radius: 50%;
			background: #fff url(../img/icons/arrow.png) no-repeat 55% center;
			box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
			transition: all 0.3s ease;
			@include hide-text;
			&--prev {
				transform: scaleX(-1);
				transform-origin: center;
				left: -5px;
				@include mq($min-width: 567px) {
					left: -12px;
				}
				@include mq($min-width: 1520px) {
					left: -75px;
				}
			}
			&--next {
				right: -5px;
				@include mq($min-width: 567px) {
					right: -12px;
				}
				@include mq($min-width: 1520px) {
					right: -75px;
				}
			}
		}
		// Ensure slides stretch to equal height
		.swiper-wrapper {
			display: flex;
			align-items: stretch;
		}
		.swiper-slide {
			box-sizing: border-box;
			height: auto;
			transition: opacity 0.3s ease, filter 0.3s ease;
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			& > div {
				height: 100%;
			}
		}
		.swiper-slide.swiper-slide-visible,
		.swiper-slide.previous-slide,
		.swiper-slide.next-slide {
			opacity: 1;
			visibility: visible;
			pointer-events: auto;
		}
	}
	&__slide {
		padding: 0 10px;
		color: #333;
		font-size: 14px;
		height: 100%;
	}
	&__slide-container {
		padding: 0px 15px 30px 15px;
		height: 100%;
		border-radius: 12px;
		background: #fff;
	}
	&__slide-image {
		width: 100%;
		display: block;
		margin-bottom: 10px;
		img {
			width: 100%;
		}
	}
	&__slide-title {
		margin-bottom: 20px;
		font-size: 24px;
		font-weight: 700;
	}
}
