@mixin about-timeline-arrow-line() {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	width: 2px;
	background-color: $color-primary;
	left: 11px;
	@media (min-width: 768px) {
		left: 50%;
		transform: translateX(-50%);
	}
}
@mixin about-timeline-arrow-head() {
	content: url(../img/icons/arrow-head.svg);
	position: absolute;
	bottom: 0;
	height: 14px;
	width: 24px;
	left: 0px;
	@media (min-width: 768px) {
		left: 50%;
		transform: translateX(-50%);
	}
}

@mixin about-timeline-dot() {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: $color-primary;
	left: 50%;
	transform: translateX(-50%);
}

.about-timeline {
	position: relative;
	padding-left: 36px;
	@media (max-width: 767.99px) {
		&::before {
			@include about-timeline-arrow-line();
		}
		&::after {
			@include about-timeline-arrow-head();
		}
	}
	@media (min-width: 768px) {
		padding-left: 0;
	}
	&__item {
		display: flex;
		flex-direction: column;
		gap: 20px;
		align-items: flex-start;
		@media (max-width: 767.99px) {
			position: relative;
			&::before {
				@include about-timeline-dot();
				left: -29px;
				top: 16px;
				transform: none;
			}
		}
	}
	&__item--start {
		@media (min-width: 768px) {
			padding-top: 230px;
			position: relative;
			&::before {
				@include about-timeline-arrow-line();
				height: 200px;
				bottom: unset;
			}
			&::after {
				@include about-timeline-arrow-head();
				top: 188px;
				bottom: unset;
			}
		}
	}
	&__item--center {
		@media (min-width: 768px) {
			align-items: center;
			text-align: center;
			max-width: 50%;
			margin: 0 auto;
		}
	}
	&__item--offset {
		@media (min-width: 768px) {
			grid-column: 2;
			justify-self: end;
		}
	}
	&__grid {
		padding: 60px 0;
		@media (min-width: 992px) {
			padding: 80px 0;
		}
		// Hide arrow on mobile, it will be shown in the main timeline container
		@media (min-width: 768px) {
			margin: 40px 0;
			position: relative;
			&::before {
				@include about-timeline-arrow-line();
			}
			&::after {
				@include about-timeline-arrow-head();
			}
		}
	}
	&__grid-row {
		display: grid;
		grid-template-columns: 1fr;
		gap: 24px;
		@media (min-width: 768px) {
			position: relative;
			grid-template-columns: 1fr 1fr;
			gap: 0 80px;
		}
		@media (min-width: 992px) {
			gap: 0 120px;
		}
		@media (min-width: 1200px) {
			gap: 0 160px;
		}
		&:not(:last-child) {
			margin-bottom: 60px;
			@media (min-width: 992px) {
				margin-bottom: 120px;
			}
		}
		&::before {
			// Hide dot on mobile, it will be shown for each timeline item
			@media (min-width: 768px) {
				@include about-timeline-dot();
			}
		}
	}
	&__date {
		padding: 8px 24px;
		display: inline-block;
		font-family: $font-family-secondary;
		font-weight: 700;
		font-size: 24px;
		line-height: 1;
		background-color: $card-bg;
		border-radius: 36px;
	}
}

.about-placeholder-card {
	display: none;
	@media (min-width: 992px) {
		display: block;
	}
}

.about-person {
	display: flex;
	flex-direction: column;
	gap: 40px;
	@media (min-width: 992px) {
		gap: 80px;
	}
	&__title {
		@extend .h2;
	}
	&__img {
		display: block;
		border-radius: 20px;
	}
	&__text {
		font-size: 16px;
		font-weight: 700;
		font-family: $font-family-secondary;
		@media (min-width: 768px) {
			font-size: 20px;
		}
	}
}
