@use '../base/functions' as *;

.table-of-content {
	@media (min-width: 992px) {
		position: sticky;
		top: 100px;
	}
	ul {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		gap: 8px;
		@media (min-width: 992px) {
			gap: 16px;
			flex-direction: column;
		}
	}
	a {
		display: block;
		padding: 12px;
		border-radius: 12px;
		text-decoration: none;
		font-size: 16px;
		font-weight: 700;
		color: $text-color;
		&:hover,
		&:focus {
			background-color: color-opacity($color-primary-light, 0.6);
		}
		&.active {
			background-color: $color-primary-light;
		}
	}
}

.aside-content {
	display: flex;
	flex-direction: column;
	gap: 48px;
	@media (min-width: 992px) {
		flex-direction: row;
		gap: 120px;
	}
	&__aside {
		flex: 0 0 auto;
	}

	&__content {
		flex: 1 1 auto;
	}
}
.text-sections {
	&__section {
		&:not(:last-child) {
			margin-bottom: 60px;
			@media (min-width: 992px) {
				margin-bottom: 120px;
			}
		}
		h2 {
			margin-bottom: 28px;
			@media (min-width: 992px) {
				margin-bottom: 44px;
			}
		}
		p,
		ul,
		ol {
			margin-top: 20px;
		}
		ul {
			--list-gap: 8px;
		}
	}
}
