@use '../base/variables' as base;

// @import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wdth,wght@0,75..100,300..800;1,75..100,300..800&display=swap');

/* nexa-700 */
@font-face {
	font-display: swap;
	font-family: 'Nexa';
	font-style: normal;
	font-weight: 700;
	src: url('#{base.$s3url}/fonts/Nexa-Bold.woff2') format('woff2');
}

/* nexa-900 */
@font-face {
	font-display: swap;
	font-family: 'Nexa';
	font-style: normal;
	font-weight: 900;
	src: url('#{base.$s3url}/fonts/Nexa-Black.woff2') format('woff2');
}

