@use '../base/functions' as *;

.tile {
	padding: 24px;
	height: 100%;
	gap: 24px;
	display: flex;
	flex-direction: column;
	border-radius: 20px;
	background: #fff;
	&__title {
		@extend .h3;
	}
	&--row {
		flex-direction: row;
	}
	&--large {
		@media (min-width: 768px) {
			padding: 32px;
		}
	}
	&--check-before {
		// Remove after Marina page update
		flex-direction: row;
		@extend .check-before;
		gap: $gap-list-icon;
	}
}

// Remove after Marina page update
.card {
	@extend .tile;
	--card-space-block: 20px;
	--card-space-inline: 16px;
	gap: 24px;
	font-size: 16px;
	&--p-vertical-small {
		--card-space-block: 16px;
	}
	&--gap-small {
		gap: 16px;
	}
	&--no-padding {
		padding: 0;
	}
	&--text-medium {
		font-size: 16px;
	}
	&--text-large {
		font-size: 18px;
	}
	&--text-small {
		font-size: 12px;
	}
	&__header {
		padding: var(--card-space-block) var(--card-space-inline) 0;
		&--no-padding {
			padding: 0;
			.card__image--full img {
				border-radius: 12px 12px 0 0;
			}
		}
	}
	&__body {
		padding: 0 var(--card-space-inline);
		&--no-padding {
			padding: 0;
		}
	}
	&__grid {
		display: flex;
		flex-direction: column;
		gap: 24px;
	}
	&__footer {
		padding: 0 var(--card-space-inline) var(--card-space-block);
		&--no-padding {
			padding: 0;
		}
	}
	&__image {
		img {
			max-width: 100%;
			height: auto;
			border-radius: 12px;
			object-fit: cover;
			display: inline-block;
		}
		&--full {
			width: 100%;
			img {
				width: 100%;
			}
		}
	}
	&__title {
		@extend .h3;
	}
	&__subtitle {
		@extend .h6;
		font-weight: 600;
	}
}

.plain-card {
	text-align: center;
	@media (min-width: 576px) {
		text-align: left;
	}
	&__title {
		@extend .h3;
		color: var(--text-color);
	}
	&__icon {
		width: 48px;
		height: 48px;
		margin-bottom: 24px;
	}
	&__content {
		position: relative;
		display: flex;
		flex-direction: column;
		gap: 12px;
	}
	&--text-opacity {
		color: color-opacity($text-color-inverse, 0.75);
	}
	&--counter {
		counter-increment: counter;
		padding-top: 64px;
		position: relative;
		@media (min-width: 768px) {
			padding-top: 138px;
		}
		&::before {
			content: counter(counter);
			position: absolute;
			left: 0px;
			top: 0;
			font-family: $font-family-secondary;
			font-weight: 900;
			font-size: 100px;
			line-height: 1;
			color: #ccd2fb;
			@media (min-width: 576px) {
				left: -8px;
			}
			@media (min-width: 768px) {
				font-size: 200px;
			}
		}
	}
}

.card-feature {
	height: 100%;
	width: 330px;
	display: flex;
	flex-direction: column;
	border-radius: 16px;
	background-color: $card-bg;
	text-decoration: none;
	color: $text-color;
	@media (max-width: 420px) {
		width: 100%;
	}
	&--fluid {
		width: 100%;
	}
	&--text-small {
		font-size: 12px;
	}
	&--text-small &__title {
		@extend .h4;
	}
	&--auto-height &__content {
		min-height: auto;
	}
	&__wrap-link {
		height: 100%;
		display: flex;
		flex-direction: column;
		color: inherit;
		text-decoration: none;
		&:hover {
			color: inherit;
		}
	}
	&__wrap-link:hover &__title {
		color: $link-color;
	}
	&__wrap-link:hover &__content {
		background-color: color-opacity($color-primary, 0.1);
	}
	&__cover {
		border-top-left-radius: 16px;
		border-top-right-radius: 16px;
		overflow: hidden;
		img {
			width: 100%;
			height: 230px;
			object-fit: cover;
			display: block;
		}
	}
	&__title {
		@extend .h3;
	}
	&__content {
		padding: 24px;
		min-height: 180px;
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		gap: 12px;
		align-items: flex-start;
		border-bottom-left-radius: 16px;
		border-bottom-right-radius: 16px;
		@media (min-width: 992px) {
			min-height: 230px;
		}
	}
	&__button {
		margin-top: auto;
	}
}

.stocking-cards {
	scroll-margin-top: 100px;
	position: relative;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 24px;
	@media (max-width: 576px) {
		display: flex;
		flex-direction: column;
		gap: 32px;
	}
	@media (max-width: 992px) {
		grid-template-columns: repeat(2, 1fr);
	}
	&__item {
		@media (max-width: 576px) {
			position: sticky;
			top: 92px;
		}
	}
}

.card-full {
	position: relative;
	min-height: 350px;
	padding: 24px;
	background-color: $color-dark;
	border-radius: 24px;
	// @extend .net-bg;
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
	}
	&__content {
		position: relative;
		@extend .h4;
		color: $text-color-inverse;
	}
	&__title {
		@extend .h2;
		color: $text-color-inverse;
		margin-bottom: 24px;
	}
	&__media-bg {
		border-radius: inherit;
		@extend .block__media-bg;
	}
}

.icon-link-card {
	border-bottom: 1px solid $strokes;
	padding: 24px 12px;
	color: var(--text-color, $text-color);
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 16px;
	text-decoration: none;
	transition: all 0.5s ease;
	@media (min-width: 576px) {
		gap: 32px;
	}
	&:hover {
		background-color: rgba(251, 253, 255, 0.2);
		color: inherit;
	}
	&__content {
		font-size: 16px;
		display: flex;
		flex-direction: column;
		gap: 16px;
	}
	&__title {
		@extend .h3;
		color: inherit;
	}
	&__icon {
		flex: 0 0 auto;
		display: block;
		width: 32px;
		height: 32px;
		border-radius: 50%;
		border: 1px solid var(--inverse-color, $strokes);
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			width: 20px;
		}
		@media (min-width: 576px) {
			width: 64px;
			height: 64px;
			svg {
				width: 24px;
			}
		}
	}
}

.quote {
	blockquote {
		position: relative;
		margin: 8px 0 0;
		padding: 24px;
		background: $color-dark;
		color: $color-dark-inverse;
		font-size: 16px;
		font-weight: 400;
		border-radius: 20px;
		line-height: 24px;
		&::before {
			content: '';
			position: absolute;
			top: -12px;
			left: 18px;
			font-size: 40px;
			line-height: 1;
			color: $color-primary;
			font-family: $font-family-secondary;
		}
	}
	&__author {
		margin-top: 20px;
		padding-left: 24px;
		display: flex;
		gap: 16px;
	}
	&__author-avatar {
		border-radius: 50%;
		width: 48px;
		height: 48px;
		overflow: hidden;
		background-color: #d9d9d9;
		flex: 0 0 auto;
		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__author-name {
		margin-bottom: 4px;
		display: block;
		font-size: 16px;
		font-style: normal;
		font-weight: 700;
	}
	&__author-position {
		font-weight: 400;
		font-size: 12px;
		line-height: 20px;
	}
}

.icon-card {
	height: 100%;
	padding: 24px;
	display: flex;
	flex-direction: column;
	gap: 24px;
	background-color: #f5f7fa;
	color: $text-color;
	border-radius: 16px;
	&__header {
		display: flex;
		align-items: center;
		gap: 12px;
		span {
			text-transform: uppercase;
			font-weight: 600;
			font-size: 12px;
			line-height: 20px;
			color: $strokes;
		}
	}
	&__icon {
		padding: 16px;
		width: 72px;
		height: 72px;
		flex: 0 0 auto;
		border-radius: 16px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
		img {
			max-width: 36px;
			max-height: 36px;
		}
	}

	&__body {
		margin-top: auto;
		display: flex;
		flex-direction: column;
		gap: 8px;
	}
	&__title {
		@extend .h4;
	}
	&--auto-height {
		height: auto;
	}
	&--auto-height &__body {
		margin-top: 0;
	}
}

.full-card {
	position: relative;
	min-height: 350px;
	padding: 24px;
	color: #fff;
	font-size: 20px;
	line-height: 26px;
	font-weight: 700;
	font-family: $font-family-secondary;
	border-radius: 24px;
	background-color: $color-dark;
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
	}
	&__title {
		margin-bottom: 24px;
		@extend .h2;
		color: #fff;
	}
	&__media-bg {
		@extend .block__media-bg;
		border-radius: inherit;
		& + * {
			position: relative;
		}
	}
}

.placeholder-card {
	border-radius: 16px;
	height: 100%;
	overflow: hidden;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		display: block;
	}
}

.card-horizontal {
	display: flex;
	flex-direction: column;
	gap: 12px;
	border-radius: 8px;
	background-color: $card-bg;
	padding: 16px 24px;
	@media (min-width: 768px) {
		flex-direction: row;
		align-items: center;
		gap: 24px;
	}
	&__header {
		@media (min-width: 768px) {
			width: 30%;
		}
		@media (min-width: 992px) {
			width: 40%;
			padding-right: 15%;
		}
	}
	&__body {
		flex: 1;
		@media (min-width: 992px) {
			padding-right: 15%;
		}
	}
	&__title {
		text-align: center;
		@media (min-width: 768px) {
			text-align: left;
		}
	}
	&__icon {
		align-self: center;
		@media (min-width: 768px) {
			margin-left: auto;
			order: 1;
		}
	}
	& + & {
		margin-top: 32px;
	}
	&--primary {
		background-color: $color-primary;
		color: #fff;
	}
	&--primary &__title {
		color: inherit;
	}
}

.card-bento {
	height: 100%;
	min-height: 260px;
	padding: 24px;
	display: flex;
	flex-direction: column;
	gap: 12px;
	border-radius: 20px;
	background-color: $card-bg;
	color: #5b5c5c;
	@media (min-width: 992px) {
		padding-right: 15%;
	}
	&__header {
		img {
			margin-bottom: 12px;
		}
	}
	&__title {
	}
	&__body {
		font-size: 16px;
		line-height: 24px;
	}
	&--text-bottom {
		@media (min-width: 768px) {
			justify-content: flex-end;
		}
	}
	&--primary {
		background-color: $color-primary;
		color: #fff;
		--bg-color: #{$color-primary};
	}
	&--dark {
		background-color: $color-dark;
		color: $color-dark-inverse;
		--bg-color: #{$color-dark};
	}
	&--primary &__title,
	&--dark &__title {
		color: inherit;
	}
}

.card-net-bg {
	// Variables:
	// --bg-color: // Set background color if different from card background
	// --x-gap: 72px; // Horizontal gap
	// --y-gap: 72px; // Vertical gap
	--line: 1px;
	--default-net-color: #{color-opacity($color-primary, 0.5)};
	position: relative;
	// Base grid
	background-image: linear-gradient(to right, var(--net-color, var(--default-net-color)) 0 var(--line), transparent var(--line) 100%),
		linear-gradient(to bottom, var(--net-color, var(--default-net-color)) 0 var(--line), transparent var(--line) 100%);
	background-size: var(--x-gap, 72px) var(--y-gap, 72px), var(--x-gap, 72px) var(--y-gap, 72px);
	background-position: calc(var(--x-gap, 72px) / 2) calc(var(--y-gap, 72px) / 2), calc(var(--x-gap, 72px) / 2) calc(var(--y-gap, 72px) / 2);
	background-repeat: repeat;

	// White theme
	&--net-color-white {
		--net-color: #{color-opacity(#fff, 0.5)};
	}
	&--net-color-dark {
		--net-color: #{color-opacity($color-dark-inverse, 0.5)};
	}
	&--net-color-strokes {
		--net-color: #{color-opacity($strokes, 0.5)};
	}
	&--bg-color-primary {
		--bg-color: #{$color-primary};
	}
	&--bg-color-dark {
		--bg-color: #{$color-dark};
	}

	// Side fade modifiers
	&--right,
	&--left,
	&--bottom,
	&--top {
		overflow: hidden;
		&::before {
			content: '';
			position: absolute;
			inset: 0;
			background: var(--side-gradient);
		}
		& > * {
			position: relative;
		}
	}

	&--right {
		--side-gradient: linear-gradient(to right, var(--bg-color, #{$card-bg}) var(--side-position, 60%), transparent 100%);
	}

	&--left {
		--side-gradient: linear-gradient(to left, var(--bg-color, #{$card-bg}) var(--side-position, 60%), transparent 100%);
	}

	&--bottom {
		--side-gradient: linear-gradient(to bottom, var(--bg-color, #{$card-bg}) var(--side-position, 60%), transparent 100%);
	}
	&--top {
		--side-gradient: linear-gradient(to top, var(--bg-color, #{$card-bg}) var(--side-position, 60%), transparent 100%);
	}
}

.card-grouped {
	--text-color: #5b5c5c;
	padding: 40px;
	border-radius: 20px;
	background-color: $card-bg;
	display: flex;
	flex-direction: column;
	gap: 24px;
	color: var(--text-color);
	@media (min-width: 768px) {
		gap: 40px;
	}
	@media (min-width: 992px) {
		gap: 80px;
	}
	&__header {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		gap: 16px;
		@media (min-width: 576px) {
			flex-direction: row;
		}
	}
	&__header-content {
		flex: 1 1 auto;
		display: flex;
		flex-direction: column;
		gap: 8px;
	}
	&__header-aside {
		flex: 0 0 auto;
		text-align: center;
	}
	&__title {
		margin-bottom: 4px;
	}
	&--primary {
		background-color: $color-primary;
		--text-color: #fff;
	}
	&--dark {
		background-color: $color-dark;
		--text-color: #{$color-dark-inverse};
	}
	&--primary &__title,
	&--dark &__title,
	&--primary &__subtitle,
	&--dark &__subtitle {
		color: var(--text-color);
	}
}

.stat-card {
	&__stat {
		display: block;
		margin-bottom: 16px;
		color: $strokes;
	}
	&__title {
		margin-bottom: 12px;
		color: var(--text-color);
	}
}
