.modal {
	display: none;
	&__success {
		text-align: center;
		max-width: 450px;
		&-icon {
			font-size: 3em;
			color: #4bb543;
			padding-bottom: 0.5rem;
		}
		h2 {
			padding-bottom: 1rem;
			font-size: 2.2rem;
		}
	}
}

.thank {
	padding: 90px 0 120px 0;
	text-align: center;
	background: #f6f6f6;
	h1 {
		color: #2e3949;
		font-size: 42px;
		font-weight: 900;
		padding-bottom: 15px;
	}
	img {
		max-width: 100%;
	}
	h2 {
		color: #2e3949;
		font-size: 30px;
		font-weight: 700;
		padding-bottom: 50px;
	}
}

.thankyou {
	background: #f6f6f6;
	padding: 70px 0 0 0;
	flex-grow: 1;
	h1 {
		font-size: 50px;
		font-weight: 700;
		color: #2e3949;
		text-align: center;
		padding-bottom: 70px;
	}
	img {
		display: block;
		margin: 0 auto;
		width: 100%;
	}
	iframe {
		min-height: 700px;
		height: 100vh;
		width: 100%;
		margin-bottom: 70px;
	}
	@include mq($max-width: 767px) {
		padding-top: 30px;
		h1 {
			font-size: 30px;
			padding-bottom: 30px;
		}
		iframe {
			margin-bottom: 30px;
		}
	}
}
