.wrapper {
	// height: fill-available;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	position: relative;
	padding-top: 72px;
	background-color: $bg-1;
	background-image: $bg-img;
	background-position-x: center;
	&--no-header {
		padding-top: 0;
	}
	&--no-bg {
		background-image: none;
	}
}

.main {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
}

.index-landing {
	padding-top: 100px;
}
