body,
html {
	scroll-behavior: smooth;
}
html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	padding: 0;
	margin: 0;
	font-family: $font-family;
	font-size: $font-size;
	font-weight: normal;
	color: $text-color;
	line-height: 1.2;
}

input[type='submit'] {
	cursor: pointer;
}

input:where(:not([type='checkbox'], [type='radio'], [type='submit'])) {
	@include input('');
	&:focus,
	&:active,
	&:target {
		@include input-focus('');
	}
	&[aria-invalid='true'] {
		border-color: $color-danger;
	}
}

select {
	@include input('');
	&:focus,
	&:active,
	&:target {
		@include input-focus('');
	}
}

textarea {
	min-height: 90px;
	@include input('');
	&:focus,
	&:active,
	&:target {
		@include input-focus('');
	}
}

input::placeholder {
	color: $strokes;
}

textarea::placeholder {
	color: $strokes;
}

label {
	display: block;
	font-size: 12px;
	font-weight: 400;
	line-height: 20px;
	color: $strokes;
}

video::-webkit-media-controls {
	display: none;
}

a {
	color: $link-color;
	&:hover {
		color: $link-color-hover;
	}
}

img {
	@include mq($max-width: 992px) {
		max-width: 100%;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: inherit;
	font-weight: normal;
}
h1 {
	font-size: 24px;
}
h2 {
	font-size: 22px;
}
h3 {
	font-size: 20px;
}
h4 {
	font-size: 18px;
}
h5 {
	font-size: 16px;
}
h6 {
	font-size: 14px;
}
mark {
	background-color: $color-primary-light;
}
