.customer-hero {
	margin-bottom: 60px;
	&__logo {
		max-width: 240px;
		max-height: 160px;
		height: auto;
	}
}

.customer-bread {
	margin-bottom: 32px;
	color: #212020;
	font-size: 16px;
	font-weight: 400;
	line-height: 1.5;
	letter-spacing: 0.7px;
	li {
		display: inline-block;
		&.active {
			font-weight: 700;
		}
		&:last-child a {
			pointer-events: none;
		}
	}
	a {
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}

.customer-content {
	font-size: 16px;
	font-weight: 400;
	line-height: 1.33;
	h2 {
		@extend .h3;
		padding: 16px 0;
	}
	h3 {
		@extend .h3;
		padding: 12px 0;
	}
	h4 {
		@extend .h4;
		padding: 12px 0;
	}
	ul {
		@extend .list;
		@extend .list--dot;
	}
	ol {
		@extend .list;
		@extend .list--numbered;
	}
	p {
		padding-bottom: 16px;
	}
	a {
		color: $link-color;
		&:hover {
			color: $link-color-hover;
		}
	}
}

.customer-carousel {
	position: relative;
	&__item {
		margin-bottom: 16px;
	}
	.swiper-arrow {
		position: absolute;
		top: calc(50% - 28px);
		z-index: 10;
		cursor: pointer;
		width: 48px;
		height: 48px;
		border-radius: 50%;
		background: #fff url(../img/icons/arrow.png) no-repeat 55% center;
		box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
		transition: all 0.3s ease;
		@include hide-text;

		&--prev {
			transform: scaleX(-1);
			transform-origin: center;
			left: -5px;
			@include mq($min-width: 567px) {
				left: -12px;
			}
			@include mq($min-width: 1440px) {
				left: -56px;
			}
		}
		&--next {
			right: -5px;
			@include mq($min-width: 567px) {
				right: -12px;
			}
			@include mq($min-width: 1440px) {
				right: -56px;
			}
		}
	}
}
