.portal-sign-up {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	overflow: hidden;

	&__wrapper {
		position: relative;
		padding: 60px 0;
		@media (min-width: 992px) {
			padding: 96px 0;
		}
	}
	&__content {
		text-align: center;
		font-weight: 400;
		font-size: 16px;
		line-height: 22px;
		display: flex;
		flex-direction: column;
		gap: 24px;
		@media (min-width: 768px) {
			text-align: left;
		}
	}
}

