@use './../utilities/flags/flag-icons.scss';
// Flags - https://flagicons.lipis.dev/

$header-height: 76px;
$menu-mq-max: 992px;
$menu-mq-min: 991.99px;

.header {
	position: fixed;
	z-index: 100;
	width: 100%;
	top: 0;
	left: 0;
	&__wrapper {
		padding-top: 10px;
		padding-bottom: 10px;
		display: flex;
		gap: 16px;
		align-items: center;
	}
	&--fixed:not(.active) {
		backdrop-filter: blur(16px);
		background-color: rgba(255, 255, 255, 0.7);
	}
	&__menu {
		align-self: stretch;
		@media (max-width: 768px) {
			margin-inline-start: auto;
		}
		@media (min-width: $menu-mq-min) {
			flex-grow: 1;
		}
	}
	&__button {
		margin-inline-start: auto;
		@media (max-width: 768px) {
			display: none;
		}
		@media (min-width: $menu-mq-min) {
			order: 1;
		}
	}
	&__logo,
	&__button {
		z-index: 1100;
		position: relative;
	}
}

.logo {
	display: block;
	img {
		display: block;
		max-width: 230px;
		width: 100%;
	}
}

.menu {
	height: 100%;
	z-index: 1099;
	position: relative;
	@media (max-width: $menu-mq-max) {
		position: fixed;
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100vh;
		height: 100dvh;
		background-color: #ffffff;
		inset: 0;
		overflow: hidden;
		z-index: 1105;
		transform: translateY(-100%);
		&.animated {
			transition: transform 0.3s ease;
		}
		&.active {
			transform: translateY(0%);
		}
	}
	&__list {
		height: 100%;
		display: flex;
		justify-content: space-around;
		flex-grow: 1;
		@media (max-width: $menu-mq-max) {
			height: auto;
			flex-direction: column;
			justify-content: start;
			align-items: stretch;
			overflow: hidden auto;
			gap: 16px;
			position: relative;
		}
	}
	&__item {
		height: 100%;
		&--mobile {
			display: none;
			@media (max-width: $menu-mq-max) {
				display: block;
			}
		}
		&--button {
			padding: 12px 0px;
			a {
				width: 100%;
			}
		}
		@media (max-width: $menu-mq-max) {
			line-height: 1;
			height: auto;
			margin: 0;
			display: block;
		}
	}
	&__item--has-submenu &__link-title {
		@media (max-width: $menu-mq-max) {
			&::after {
				padding-inline-start: 16px;
				color: #1c1724;
				font-family: 'Font Awesome 5 Free';
				display: inline-block;
				line-height: 1;
				content: '\f054';
				font-size: 14px;
				font-weight: 700;
			}
			// change chevron for RTL
			:where(.rtl) &::after {
				content: '\f053';
			}
		}
	}
	&__link-title {
		height: 100%;
		font-size: 16px;
		font-weight: 400;
		color: #1c1724;
		position: relative;
		transition: color 0.3s ease;
		text-decoration: none;
		cursor: pointer;
		display: flex;
		align-items: center;
		z-index: 1100;
		-webkit-tap-highlight-color: transparent;

		@media (max-width: $menu-mq-max) {
			min-height: 40px;
			display: flex;
			align-items: center;
			justify-content: start;
		}
	}

	&__link-title:hover:not(.menu__link-title--no-hover),
	&__item:hover .menu__link-title:not(.menu__link-title--no-hover) {
		@media (min-width: $menu-mq-min) {
			text-decoration: underline;
			text-decoration-color: $link-color;
			text-underline-offset: 4px;
		}
	}

	&__dropdown {
		position: absolute;
		left: 100%;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: #fff;
		z-index: 1101;
		transition: left 0.3s ease;
		overflow: hidden auto;
		@media (min-width: $menu-mq-min) {
			position: fixed;
			padding: 96px 32px 24px;
			left: 0;
			height: auto;
			background-color: initial;
			z-index: 1099;
			opacity: 0;
			visibility: hidden;
			&:after {
				position: absolute;
				content: '';
				inset: 0;
				background-color: #fff;
				z-index: -1;
			}
		}
		&.active {
			opacity: 1;
			visibility: visible;
			@media (max-width: $menu-mq-max) {
				height: 100%;
				left: 0;
			}
		}
		a:not(.menu-all-card__button) {
			color: #5d5c5d;
			font-size: 18px;
			line-height: 1.5;
			font-weight: 500;
			text-decoration: none;
			-webkit-tap-highlight-color: transparent;
			&:hover,
			&:active {
				color: $link-color;
			}
			@media (max-width: $menu-mq-max) {
				font-size: 16px;
			}
		}
	}

	// Mobile head
	&__mobile-head {
		padding: 8px 12px;
		min-height: 64px;
		display: none;
		align-items: stretch;
		justify-content: space-between;
		position: sticky;
		z-index: 105;
		background-color: #ffffff;
		top: 0;
		gap: 8px;
		@media (max-width: $menu-mq-max) {
			display: flex;
			&.active .menu__back {
				display: flex;
			}
		}
	}
	&__back,
	&__close {
		width: 40px;
		height: 40px;
		cursor: pointer;
		color: #151515;
		font-size: 16px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex: 0 0 auto;
		background-color: #e3eeff;
		border-radius: 50%;
		border: none;
		-webkit-tap-highlight-color: transparent;
		&:hover {
			background-color: #d4e0ff;
		}
	}
	&__back {
		display: none;
	}
	&__current-title {
		font-size: 18px;
		font-weight: 500;
		color: #151515;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
	}
}

.categories-menu {
	max-height: 100vh;
	overflow-y: auto;
	display: flex;
	gap: 72px;

	@media (max-width: $menu-mq-max) {
		display: block;
		max-width: none;
		padding: 0;
	}
	// &__list + &__list {
	// 	margin-top: 16px;
	// }
	&__item {
		padding: 8px 0px;
		display: flex;
		align-items: center;
		gap: 6px;
		border-radius: 12px;
		cursor: pointer;
		-webkit-tap-highlight-color: transparent;
		font-size: 16px;
		line-height: 1.5;
		font-weight: 400;

		&::after {
			padding-inline-start: 8px;
			color: #1c1724;
			font-family: 'Font Awesome 5 Free';
			display: inline-block;
			line-height: 1;
			content: '\f054';
			font-size: 14px;
			font-weight: 700;
		}
		// change chevron for RTL
		:where(.rtl) &::after {
			content: '\f053';
		}
		&--all {
			display: none;
			&::after {
				content: none;
			}
			a {
				all: unset !important;
			}
			@media (max-width: $menu-mq-max) {
				display: block;
			}
		}
		@media (min-width: $menu-mq-min) {
			font-size: 18px;
			text-transform: uppercase;
			padding: 12px 16px;
			&.active,
			&:hover,
			&:active {
				background-color: $color-primary-light;
			}
			&::after {
				content: none;
			}
		}
	}
	&__item + &__item {
		margin-top: 16px;
		@media (min-width: $menu-mq-min) {
			margin-top: 12px;
		}
	}
	&__links {
		flex-grow: 1;
		display: none;
		&.active {
			display: block;
		}
		ul {
			display: flex;
			flex-direction: column;
			gap: 16px;
			li {
				padding: 8px 0;
			}
		}
	}
	&__col-title {
		margin-bottom: 32px;
		font-size: 16px;
		font-weight: 700;
		color: #c7c7c7;
		display: none;
		@media (min-width: $menu-mq-min) {
			display: block;
		}
	}

	&__category {
		@media (max-width: $menu-mq-max) {
			grid-template-columns: 1fr;
		}
	}
	&__grid {
		display: flex;
		gap: 16px 12px;
		flex-direction: column;
		@media (min-width: $menu-mq-min) {
			flex-direction: row;
		}
	}
	&__data {
		flex: 1;
		display: flex;
		flex-direction: column;
		@media (max-width: $menu-mq-max) {
			display: block;
			position: absolute;
			left: 100%;
			top: 0;
			width: 100%;
			background-color: #fff;
			height: 100%;
			z-index: 1102;
			transition: left 0.3s ease;
			&.active {
				left: 0;
			}
		}
	}
	&__all {
		flex: 0 1 260px;
	}
}

.mobile-menu-trigger {
	display: none;
	height: 100%;
	width: 30px;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	span {
		display: block;
		height: 2px;
		background-color: #333333;
		width: 24px;
		position: relative;
		&:before,
		&:after {
			content: '';
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #333333;
		}
		&:before {
			top: -6px;
		}
		&:after {
			top: 6px;
		}
	}
	@media (max-width: $menu-mq-max) {
		display: flex;
	}
}

.language-menu {
	.menu__link-title:hover {
		text-decoration: none;
	}
	&__item {
		display: flex;
		align-items: center;
		.flag {
			width: 28px;
			height: 28px;
			border-radius: 50%;
		}
	}
	&__name {
		display: none;
		@media (max-width: $menu-mq-max) {
			display: inline-block;
		}
	}
	&__icon {
		margin-inline-end: 2px;
		@media (max-width: $menu-mq-max) {
			margin-inline-end: 8px;
		}
	}
	ul {
		display: grid;
		grid-template-columns: 1fr;
		gap: 8px 24px;
		@media (min-width: $menu-mq-min) {
			grid-template-columns: repeat(4, minmax(100px, 1fr));
		}
	}
	.language-list {
		&__item {
			display: flex;
			align-items: center;
			padding: 8px;
			gap: 8px;
			border-radius: 12px;
			text-decoration: none;
			&.active {
				background-color: rgba(0, 73, 158, 0.05);
			}
			.flag {
				flex: 0 0 auto;
				width: 32px;
				height: 32px;
				border-radius: 50%;
			}
		}
	}
}

.mobile-menu {
	@media (max-width: $menu-mq-max) {
		padding: 8px 16px;
	}
}

.menu-overlay {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 1098;
	backdrop-filter: blur(16px);
	display: none;
	&.active {
		display: block;
	}
}

.menu-all-card {
	min-width: 220px;
	min-height: 240px;
	height: 100%;
	max-height: 320px;
	padding: 24px;
	border-radius: 16px;
	background-color: $color-primary;
	color: $color-primary-light;
	font-size: 16px;
	line-height: 24px;
	font-weight: 400;
	display: flex;
	flex-direction: column;
	gap: 12px;
	background-repeat: no-repeat;
	background-position: bottom right;
	@media (max-width: $menu-mq-max) {
		display: none;
	}
	&__title {
		color: inherit !important;
	}
	&--features {
		background-image: url('../img/menu-all-features.svg');
	}
	&--solutions {
		background-image: url('../img/menu-all-solutions.svg');
	}
	a {
		margin-top: auto;
		padding-inline: 12px;
		@media (max-width: $menu-mq-max) {
		}
	}
}

.menu-all-link {
	display: block;
	padding: 8px 0;
	@media (min-width: $menu-mq-min) {
		display: none;
		padding: 0;
	}
}
