.cta-section-2 {
	text-align: center;
	&__image {
		margin-bottom: 20px;
		@media (min-width: 768px) {
			margin-bottom: 0;
		}
	}
	&__title {
		padding-bottom: 20px;
	}
	&__text {
		font-size: 18px;
		padding-bottom: 20px;
		line-height: 1.33;
	}
	&__buttons a {
		min-width: auto;
		margin-top: 12px;
	}
	@media (min-width: 768px) {
		text-align: left;
	}
}

.cta-section {
	@extend .block;
	text-align: center;
	background-color: #fff;
	@media (min-width: 768px) {
		text-align: left;
	}
	&__grid {
		display: flex;
		flex-wrap: wrap;
		gap: $gap;
		align-items: center;
		justify-content: space-between;
		@media (min-width: 768px) {
			flex-wrap: nowrap;
		}
	}
	&__content,
	&__image {
		flex: 1 1 auto;
		@media (min-width: 768px) {
			flex: 1 1 50%;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: $gap;
	}
	&__image {
		img {
			max-width: 100%;
			height: auto;
			display: block;
			@media (max-width: 991.98px) {
				max-height: 300px;
				margin-left: auto;
				margin-right: auto;
			}
		}
		@media (min-width: 768px) {
			order: 1;
		}
	}

	&__title {
		@extend .h2;
	}
	&__buttons {
		margin-top: 12px;
		display: flex;
		flex-wrap: wrap;
		gap: 12px;
		justify-content: center;
		a {
			min-width: auto;
		}
		@media (min-width: 992px) {
			justify-content: start;
		}
	}
}

.cta-card {
	border-radius: 24px;
	background-color: $color-dark;
	padding: 24px;
	display: flex;
	gap: 32px;
	flex-direction: column;
	color: #fff;
	font-size: 20px;
	line-height: 26px;
	font-weight: 700;
	font-family: $font-family-secondary;
	@media (min-width: 576px) {
		flex-direction: row;
	}
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
		flex-wrap: nowrap;
	}
	&__image {
		img {
			border-radius: 20px;
			display: block;
			width: 100%;
			aspect-ratio: 16 / 9;
			object-fit: cover;
		}
		@media (min-width: 576px) {
			align-self: start;
			img {
				aspect-ratio: 1 / 1;
				max-width: 240px;
			}
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		gap: 8px;
	}
	&__title {
		color: #fff;
	}
	&__actions {
		margin-top: 32px;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		gap: 24px;
		@media (min-width: 992px) {
			margin-top: auto;
		}
	}
	&__link {
		color: #fff;
		font-family: $font-family-secondary;
		font-size: 20px;
		line-height: 26px;
		font-weight: 700;
		&:hover {
			color: #fff;
			opacity: 0.7;
			transition: all 0.3s ease;
		}
	}
}

.net-bg {
	--x-gap-def: 33%;
	--y-gap-def: 33%;
	background-image: repeating-linear-gradient(to right, #5b5b5b 0, #5b5b5b 1px, transparent 1px, transparent var(--x-gap, var(--x-gap-def))),
		repeating-linear-gradient(to bottom, #5b5b5b 0, #5b5b5b 1px, transparent 1px, transparent var(--y-gap, var(--y-gap-def)));
	background-repeat: no-repeat;
	@media (min-width: 768px) {
		--x-gap-def: 25%;
		--y-gap-def: 260px;
		background-position: 0 0, 0 46px;
	}
}

.base-cta-card {
	min-height: 350px;
	padding: 24px;
	color: #fff;
	font-size: 20px;
	line-height: 26px;
	font-weight: 700;
	font-family: $font-family-secondary;
	border-radius: 24px;
	background-color: $color-dark;

	// @extend .net-bg;
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
	}
	&__title {
		margin-bottom: 24px;
		@extend .h1;
		color: #fff;
		@media (min-width: 992px) {
			font-size: 40px;
		}
	}
	&__buttons {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}
}

.case-card {
	border-radius: $border-radius;
	background-color: $card-bg;
	padding: 24px;
	gap: 32px;
	display: flex;
	flex-wrap: wrap;
	@media (min-width: 768px) {
		padding: 32px;
	}
	@media (min-width: 992px) {
		padding: 42px;
		gap: 40px;
		flex-wrap: nowrap;
	}
	&__tag {
		margin-bottom: 16px;
		display: block;
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		color: $color-primary;
		text-transform: uppercase;
	}
	&__title {
		margin-bottom: 32px;
		@media (min-width: 768px) {
			margin-bottom: 48px;
		}
	}
	&__text {
		@extend .h4;
	}
	&__logo {
		flex: 0 0 auto;
		width: 120px;
		img {
			width: 100%;
		}
	}
	&__action {
		flex: 0 0 auto;
	}
}

// Remove pricing-meeting after page update
.pricing-meeting {
	padding-bottom: 0;
	&__wrapper {
		padding-top: 20px;
		padding-bottom: 20px;
		background: #fff;
		@include mq($max-width: 991px) {
			padding-top: 40px;
			padding-bottom: 40px;
			text-align: center;
		}
	}
	h2 {
		padding-bottom: 20px !important;
		@include mq($max-width: 768px) {
			text-align: center;
		}
	}
	p {
		padding-bottom: 15px;
		font-size: 18px;
		line-height: 1.5;
		color: #4d4d4d;
	}
	.button-orange {
		margin-top: 20px;
	}
	img {
		@include mq($max-width: 768px) {
			padding-bottom: 20px;
		}
	}
}
