// Float
.clear {
	clear: both;
	height: 0;
	overflow: hidden;
}
.float_left {
	float: left !important;
}
.float_right {
	float: right !important;
}
.float_none {
	float: none !important;
}

// Text
.text_right {
	text-align: right !important;
}
.text_center {
	text-align: center !important;
}
.text_left {
	text-align: left !important;
}
.text_uppercase {
	text-transform: uppercase;
}
.text_italic {
	font-style: italic;
}
.text_min-lines-2 {
	min-height: 2lh;
}
.text_min-lines-3 {
	min-height: 3lh;
}
.text_balanced {
	text-wrap-style: balance;
}

// Position
.absolute {
	position: absolute !important;
}
.relative {
	position: relative !important;
}
.fixed {
	position: fixed !important;
}

// Overflow
.ovh {
	overflow: hidden;
}
.overflow_init {
	overflow: initial;
}

// No Scroll
.no-scroll {
	@include mq($max-width: 1199px) {
		overflow: hidden;
	}
}

.scroll-x {
	overflow-x: auto;
}
.scroll-y {
	overflow-y: auto;
}

//resizable  image 50% display width
.adaptive-img {
	max-width: 48vw;
	@include mq($max-width: 1100px) {
		max-width: 100%;
	}
}

// Alignments
.align-center {
	display: flex;
	justify-content: center;
	align-items: center;
}

.align-center-absolute {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

// Width
.w-full {
	width: 100%;
}
.max-w-full {
	max-width: 100%;
}
.min-w-full {
	min-width: 100%;
}
.w-auto {
	width: auto;
}

// Counter
.counter-reset {
	counter-reset: counter;
}

.counter {
	&--circle {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		height: 56px;
		border-radius: 50%;
		border: 1px solid $color-primary;
		font-size: 24px;
		line-height: 1;
		font-weight: 700;
	}
	&::before {
		counter-increment: counter;
		content: counter(counter);
	}
}
