.no-bg {
	background: none !important;
}

.bg-white {
	background-color: #fff;
}

.bg-1 {
	background-color: $bg-1;
}

.bg-bn {
	background-image: url(../img/bn-transparent.svg);
	background-position: 50% 80px;
	background-repeat: no-repeat;
	background-size: 320px;
	@media (min-width: 576px) {
		background-image: url(../img/bg-bn-full.svg);
		background-position: 45vw 50%;
		background-size: 70vw;
	}
	@media (min-width: 1400px) {
		background-size: auto;
		background-position: 45vw 45%;
	}
}

.bg-circle-lines {
	position: relative;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: url(../img/bg-vector-lines.svg);
		background-repeat: no-repeat;
		background-position: center top;
		background-size: auto;
		opacity: 0.25;
		pointer-events: none;
		@media (min-width: 1400px) {
			background-size: cover;
		}
	}
}

.gradient-1 {
	background: $gradient-1;
}

// Old styles
.dotted-bg {
	background: url(../img/bg-about.png) repeat;
	padding: 70px 0;
}

// used only on the webinar page
.page-new-bg {
	background: url(../img/pages-top-bg-graphic.png) no-repeat top;
}
