.checkout-page {
	padding-top: 0;
	@media (min-width: 992px) {
		background: linear-gradient(to right, $color-primary 0%, $color-primary 50%, #fff 50%);
	}
	.header,
	.header--fixed {
		position: static;
		background-color: $color-primary;
		backdrop-filter: none !important;
		@media (min-width: 992px) {
			background: linear-gradient(to right, $color-primary 0%, $color-primary 50%, #fff 50%);
		}
	}
}

.checkout-content {
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	display: flex;
	flex-wrap: wrap;
	@media (min-width: 992px) {
		padding-top: 32px;
		padding-right: var(--bs-gutter-x, 0.75rem);
		padding-left: var(--bs-gutter-x, 0.75rem);
		max-width: 960px;
	}
	@media (min-width: 1200px) {
		max-width: 1140px;
	}
	@media (min-width: 1400px) {
		max-width: 1320px;
	}
	&__section {
		flex: 1 1 100%;
		@media (min-width: 992px) {
			flex-basis: 50%;
		}
		&:nth-child(odd) {
			@media (min-width: 992px) {
				padding-right: 48px;
			}
			@media (min-width: 1200px) {
				padding-right: 96px;
			}
		}
		&:nth-child(even) {
			@media (min-width: 992px) {
				padding-left: 48px;
			}
			@media (min-width: 1200px) {
				padding-left: 96px;
			}
		}
	}
	&__section-inner {
		padding: 40px 12px;
		width: 100%;
		margin-right: auto;
		margin-left: auto;
		@media (min-width: 576px) {
			max-width: 540px;
		}
		@media (min-width: 768px) {
			max-width: 720px;
		}
		@media (min-width: 992px) {
			padding: 0px;
			margin-left: 0;
			margin-right: 0;
		}
	}
	&__section--bg {
		color: #fff;
		background: $color-primary;
		@media (min-width: 992px) {
			background: none;
		}
	}
	&__title {
		color: inherit;
		padding-bottom: 12px;
	}
	&__block {
		margin-bottom: 32px;
	}
	&__block-header {
		color: inherit;
		border-bottom: 1px solid #d0d0d0;
		margin-bottom: 24px;
	}
	&__block-title {
		color: inherit;
		padding-bottom: 12px;
	}
}

.checkout-expire-badge {
	display: inline-block;
	border-radius: 4px;
	padding: 6px 12px;
	background-color: #f2faf2;
	font-weight: 600;
	font-size: 12px;
	text-transform: capitalize;
	line-height: 1;
	color: #15be45;
}

.checkout-form {
	&__item {
		margin-bottom: 20px;
	}
	&__input--card {
		@extend input;
	}
	&__label {
		display: block;
		font-size: 12px;
		font-weight: 400;
		line-height: 20px;
		color: #96949a;
	}
	&__input-error {
		padding-top: 2px;
		color: $color-danger;
		font-size: 12px;
	}
	&__input[aria-invalid='true'],
	.iti input[aria-invalid='true'] {
		border-color: $color-danger;
	}
	&__submit-button {
		display: flex;
		align-items: center;
		justify-content: center;
	}
	&__spinner {
		display: none;
		svg {
			width: 24px;
			height: 24px;
		}
	}
	&__error {
		display: none;
		margin-bottom: 16px;
		color: $color-danger;
		font-weight: 500;
	}
	&__info {
		text-align: center;
		margin-top: 12px;
		color: $strokes;
		img {
			display: inline;
			vertical-align: middle;
			line-height: 1;
		}
	}
}

.checkout-summary {
	margin-top: 16px;
	font-size: 16px;
	@media (min-width: 768px) {
		margin-top: 32px;
	}
	&__row {
		padding-bottom: 12px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		gap: 12px;
		font-size: 16px;
		font-weight: 400;
		line-height: 1.5;
	}
	&__row--total {
		font-size: 24px;
		font-weight: 700;
		font-family: $font-family-secondary;
	}
	&__row--bordered {
		border-bottom: 1px solid #d0d0d0;
		margin-bottom: 12px;
	}

	.checkout-expire-badge {
		margin-left: auto;
	}
}

.checkout-help {
	margin-top: 96px;
	font-size: 12px;
}
