.title {
	color: #2e3949;
	font-size: 36px;
	font-weight: 700;
	text-align: center;
	padding-bottom: 60px;
	img {
		display: block;
		margin: 0 auto;
		margin-bottom: 20px;
	}
	@include mq($max-width: 767px) {
		font-size: 25px;
		padding-bottom: 20px;
	}
}
.subtitle {
	color: #2e3949;
	font-size: 18px;
	font-weight: 700;
	text-align: center;
	padding-bottom: 50px;
}
.title-h1 {
	font-size: 55px;
	font-weight: 500;
	line-height: normal;
	letter-spacing: 0.55px;
	color: #151515;
	&--white {
		color: #fff;
	}
	@include mq($max-width: 991px) {
		font-size: 42px;
	}
	@include mq($max-width: 767px) {
		font-size: 36px;
	}
}
.title-h2 {
	font-size: 42px;
	font-weight: 400;
	line-height: normal;
	letter-spacing: 1px;
	@include mq($max-width: 991px) {
		font-size: 30px;
		font-weight: 500;
	}
}
.title-h3 {
	font-size: 30px;
	font-weight: 400;
	line-height: normal;
	letter-spacing: 1px;
	@include mq($max-width: 991px) {
		font-size: 24px;
	}
}
.title-h4 {
	font-size: 24px;
	font-weight: 500;
	line-height: normal;
	letter-spacing: 0.75px;
	@include mq($max-width: 991px) {
		font-size: 20px;
	}
}
.title-bordered {
	padding-bottom: 30px;
	position: relative;
	font-size: 30px;
	font-weight: 500;
	font-style: normal;
	line-height: normal;
	letter-spacing: 0.75px;
	@include mq($max-width: 991px) {
		font-size: 24px;
	}
	&:before {
		position: absolute;
		display: block;
		content: '';
		width: 100px;
		height: 2px;
		background-color: #f79c6a;
		bottom: 16px;
	}
}

// New styles
.h1 {
	@include heading-base();
	font-size: 32px;
	font-weight: 900;
	@include mq($min-width: 992px) {
		font-size: 40px;
	}
}
.h2 {
	@include heading-base();
	font-size: 24px;
	@include mq($min-width: 992px) {
		font-size: 28px;
	}
}
.h3 {
	@include heading-base();
	font-size: 20px;
	@include mq($min-width: 992px) {
		font-size: 24px;
	}
}
.h4 {
	@include heading-base();
	font-size: 18px;
	@include mq($min-width: 992px) {
		font-size: 20px;
	}
}
.h5 {
	@include heading-base();
	font-size: 16px;
	@include mq($min-width: 992px) {
		font-size: 18px;
	}
}
.h6 {
	@include heading-base();
	font-size: 16px;
}
.h--white {
	color: #fff;
}
.text-white {
	color: #fff;
}
.text--limit-sm {
	max-width: 480px;
}
.text--limit-md {
	max-width: 640px;
}
.text--limit-lg {
	max-width: 800px;
}
.highlight {
	color: $color-primary;
}
