.checkout-success {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	&__content {
		text-align: center;
		@media only screen and (min-width: 576px) {
			text-align: left;
		}
		& > * {
			margin-bottom: 24px;
		}
	}
	&__title {
		@extend .h1;
		font-size: 36px;
		@media only screen and (min-width: 992px) {
			font-size: 88px;
		}
	}
	&__message {
		font-size: 18px;
		font-weight: 700;
		@media only screen and (min-width: 576px) {
			width: 45vw;
		}
		@media only screen and (min-width: 768px) {
			font-size: 20px;
			max-width: 500px;
		}
	}
	&__buttons {
		margin-top: 48px;
	}
}
